  },
  methods: {
    setLocale(locale) {
      this.$i18n.locale = locale;
      localStorage.setItem("locale", locale);
      if (locale === "ar") {
        document.querySelector("html").style.direction = "rtl";
      } else {
        document.querySelector("html").style.direction = "ltr";
      }
    }
  }
