<template>
  <div class="main-content-admin">
    <b-row>
      <b-col xl="6">
        <label for="">اسم التقرير</label>
        <b-form-input v-model="reports.reportTitle" type="text"></b-form-input>
      </b-col>
      <b-col xl="6">
        <label for="">اختار لغه التقرير</label>
        <b-form-select disabled-field="notEnabled" v-model="reports.lang">
          <b-form-select-option :value="null" disabled
            >اختار لغه التقرير</b-form-select-option
          >
          <b-form-select-option></b-form-select-option>
        </b-form-select>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="6">
        <label for="">اختار الشعار</label>
        <b-form-select disabled-field="notEnabled" v-model="reports.selectLogo">
          <b-form-select-option :value="null" disabled
            >اختار الشعار</b-form-select-option
          >
          <b-form-select-option></b-form-select-option>
        </b-form-select>
      </b-col>
      <b-col xl="6">
        <label for="">صنف ب</label>
        <b-form-select v-model="reports.filterBy" disabled-field="notEnabled">
          <b-form-select-option :value="null" disabled
            >صنف ب</b-form-select-option
          >
          <b-form-select-option></b-form-select-option>
        </b-form-select>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="12" class="mt-3">
        <h4>فترة التقرير</h4>
      </b-col>
      <b-col xl="6">
        <label>من يوم</label>
        <b-form-datepicker
          v-model="reports.startDate"
          locale="ar"
          :min="currentDate"
          :initial-date="currentDate"
          v-bind="labels || {}"
          required
          :state="StartingDateState"
        ></b-form-datepicker>
      </b-col>
      <b-col xl="6">
        <label for="">الى</label>
        <b-form-datepicker
          v-model="reports.endDate"
          locale="ar"
          :min="reports.startDate"
          v-bind="labels || {}"
          required
          :state="EndingDateState"
        ></b-form-datepicker>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="6">
        <label for="">اختار الرسم البياني</label>
        <b-form-select
          disabled-field="notEnabled"
          v-model="reports.selectCharts"
        >
          <b-form-select-option :value="null" disabled
            >اختار الرسم البياني</b-form-select-option
          >
          <b-form-select-option></b-form-select-option>
        </b-form-select>
      </b-col>
      <b-col xl="6">
        <label for="">تكرار كل</label>
        <b-form-input
          v-model="reports.repeatedEvery"
          type="text"
        ></b-form-input>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="12" class="mt-3">
        <h4>الإمضاءات</h4>
      </b-col>
      <b-col xl="6" v-for="(employee, index) in employees" :key="index">
        <h5>امضاء {{ employee.name }}</h5>
        <label for="">اسم الوظيفه</label>
        <b-form-input v-model="employee.job" type="text"></b-form-input>
        <label for="">اسم الموظف</label>
        <b-form-input v-model="employee.name" type="text"></b-form-input>
        <label for="">الطلب</label>
        <b-form-input v-model="employee.order" type="text"></b-form-input>
      </b-col>
      <b-col xl="12" class="mt-3 save-btn">
        <span class="blueBg" @click="addNew">اضافة امضاء</span>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="12" class="mt-3">
        <h4>شهادة وحدة الجودة</h4>
      </b-col>
      <b-col xl="6">
        <label for="">رقم النموذج</label>
        <b-form-input v-model="reports.modelId" type="text"></b-form-input>
      </b-col>
      <b-col xl="6">
        <label for="">الاصدار</label>
        <b-form-input v-model="reports.version" type="text"></b-form-input>
      </b-col>
    </b-row>
    <div class="save-btn">
      <button type="submit" class="blueBg">
        حفظ
      </button>
      <span class="redBg">الغاء</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "CreateReports",
  data() {
    return {
      currentDate: new Date().toISOString().substring(0, 10),
      labels: {
        labelNoDateSelected: "لم يتم اختيار التاريخ",
        labelNoTimeSelected: "لم يتم اختيار الوقت",
        labelCloseButton: "اختار"
      },
      employees: [
        {
          name: "",
          job: "",
          order: ""
        }
      ],
      reports: {
        reportTitle: "",
        lang: null,
        selectLogo: null,
        filterBy: null,
        startDate: "",
        endDate: "",
        selectCharts: "",
        repeatedEvery: "",
        selectColumns: "",
        jobTitle: "",
        employeeName: "",
        order: "",
        modelId: "",
        version: ""
      }
    };
  },
  computed: {
    StartingDateState() {
      if (this.reports.startDate.length == 0) {
        return null;
      } else if (this.reports.startDate.length > 9) {
        return true;
      } else {
        return false;
      }
    },
    EndingDateState() {
      if (this.reports.endDate.length == 0) {
        return null;
      } else if (this.reports.endDate.length > 9) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    addNew() {
      this.employees.push({
        name: "",
        job: "",
        order: ""
      });
    }
  }
};
</script>
