<template>
        <div class="main-content-admin">
            <h3 class="text-center">الشروط</h3>
            <div class="housing-conditions">
                <div class="boxContianer">
                    <div class="condition mt-2" v-for="condition in conditions" :key="condition.id">
                        <input type="checkbox">
                        <p>{{condition.description_ar}}</p>
                    </div>
                     <div class="save-btn">
                    <button type="submit" class="blueBg">
                        حفظ
                    </button>
                    <span class="redBg">إلغاء</span>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>
import axios from 'axios'
import store from '../../store'
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
    name: "Conditions",
    data(){
        return {
            conditions: ""
        }
    },
    mounted(){
        axios
        .get(store.state.domain + '/housingPriorities/index', config)
        .then(res => this.conditions = res.data.data)
    }
}
</script>
