<template>
  <div class="main-content-admin">
    <div class="boxContianer">
      <h4>قائمة المخالفات</h4>
      <div class="table-style">
        <table class="grey-style">
          <tr>
            <th>
              <input
                type="checkbox"
                @click="select_all_via_check_box"
                v-model="all_select"
              />
            </th>
            <th>نوع المخالفة</th>
            <th>عقوبة الانذار الأول</th>
            <th>عقوبة الإنذار الثاني</th>
            <th>عقوبة الإنذار الثالث</th>
            <th>تعديل</th>
          </tr>
          <tr v-for="(infraction, index) in infractions" :key="index">
            <td>
              <input
                type="checkbox"
                v-model="updateItems"
                :value="infraction.id"
              />
            </td>
            <td>{{ infraction.name_ar }}</td>
            <td>-</td>
            <td>-</td>
            <td>-</td>
            <td>
              <span class="blueBg"
                ><router-link
                  :to="{
                    name: 'editInfraction',
                    params: { id: infraction.id }
                  }"
                  >تعديل</router-link
                ></span
              >
            </td>
          </tr>
          <tr v-if="emptyData()">
            <td colspan="8">
              <h4 class="text-center">لا توجد مخالفات مسجلة</h4>
            </td>
          </tr>
        </table>

        <div class="tabel-btns-container save-btn">
          <router-link class="link-to blueBg" to="/infractions/Add"
            >إضافة مخالفة</router-link
          >
          <span class="redBg" @click="deleteData()">حذف</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store/index.js";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
      }
};
export default {
  name: "infractions",
  data() {
    return {
      all_select: false,
      infractions: [],
      updateItems: []
    };
  },
  mounted() {
    this.getTableResults();
  },
  methods: {
    getTableResults() {
      axios
        .get(store.state.domain + "/infractions", config)
        .then(res => (this.infractions = res.data.data));
    },
    select_all_via_check_box() {
      if (this.all_select == false) {
        this.all_select = true;
        this.infractions.forEach(infraction => {
          this.updateItems.push(infraction.id, infraction.status);
        });
      } else {
        this.all_select = false;
        this.updateItems = [];
      }
    },
    emptyData() {
      return this.infractions.length < 1;
    },
    deleteData() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios
          .delete(store.state.domain + "/infractions/destroy/" + id, { id: id, headers: {
            localization: store.state.localization
      }})
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.getTableResults();
            }
          });
      });
    }
  }
};
</script>
