<template>
<div>
  <span v-b-modal.modal-1 v-on:click="isHidden = false" class="add-btn">اضافة تصنيف</span>
  <b-modal hide-footer id="modal-1" title="اضافة فئه" v-if="isHidden == false">
  <div class="add-category" >
    <div class="add-category-pop-up">
      <div class="mid-header">
        <h2>اضف تصنيف جديده</h2>
        <b-row>
          <b-col md="12" xl="12">
            <label>ادخل الفئه باللغه العربيه</label>
            <b-form-input
            type="text"
            v-model="category.categoryNameAr"
            :state="categoryArState"
          ></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم التصنيف باللغة العربية
          </b-form-invalid-feedback>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="12" xl="12">
            <label>ادخل الفئه باللغه الانجليزيه</label>
            <b-form-input
            type="text"
            v-model="category.categoryNameEn"
            :state="categoryEnState"
          ></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم التصنيف باللغة الانجليزية
          </b-form-invalid-feedback>
        </b-col>
        </b-row>
        <b-row v-if="err.length != 0">
        <ul class="form--error">
          <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
          <li
            v-if="
              this.err.name_ar == 'The name ar field is required.'">
            اسم التصنيف باللغة العربية
          </li>
            <li
            v-if="
              this.err.name_en == 'The name en field is required.'">
            اسم التصنيف باللغة الانجليزية
          </li>
          <li
            v-if="
              this.err.name_ar == 'The name ar has already been taken.'">
            اسم التصنيف موجود مسبقا باللغة العربية
          </li>
          <li
            v-if="
              this.err.name_en == 'The name en has already been taken.'">
           اسم التصنيف موجود مسبقا باللغة الانجليزية
          </li>
        </ul>
      </b-row>
      <div class="save-btn">
        <button
          type="submit"
          class="blueBg"
          @click.prevent="OnSubmit"
        >
          حفظ
        </button>
      </div>
      </div>
    </div>
  </div>
  </b-modal>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
export default {
   props: ['addCategoryApi', 'reloadMealCategoryResults', 'reloadIngredientCategoryResults', "reloadToolsCategoryResults"],
   name: "addCategory",
  data() {
    return {
      err: [],
      category: {
        categoryNameAr: '',
        categoryNameEn: '',
      },
      isHidden: false
    }     
  },
  computed: {
    categoryArState() {
      if (this.category.categoryNameAr.length == 0) {
        return null; 
      } else if (!this.category.categoryNameAr.match(/[\u0600-\u06FF\u0750-\u077F]/)) {
        return false
      } else if (this.category.categoryNameAr.length > 3) {
        return true;
      } else {
        return false;
      }
    },
    categoryEnState() {
      if (this.category.categoryNameEn.length == 0) {
        return null; 
      } else if (!this.category.categoryNameEn.match(/^[a-zA-Z\s]*$/)) {
        return false
      } else if (this.category.categoryNameEn.length > 3) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    OnSubmit: function(){
      axios({
        method: "POST",
        url: store.state.domain + this.addCategoryApi,
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.category.categoryNameAr,
          name_en: this.category.categoryNameEn
        }
      })
      .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.isHidden= true,
            this.category.categoryNameAr= '',
            this.category.categoryNameEn= '';
              if(this.addCategoryApi == "/kitchenCategory") {
                this.reloadToolsCategoryResults();
              } else if (this.addCategoryApi == "/mealtype") {
                this.reloadMealCategoryResults();
              } else {
                this.reloadIngredientCategoryResults();
              }
          } else{
            this.isHidden= false
          }
        } 
      ).catch(error => {
          this.err = error.response.data.errors;
      });
    }
  }
}
</script>
