<template>
  <div class="main-content-admin">
    <div class="boxContianer">
      <h4>قائمة مخالفات الطلاب</h4>
      <div class="table-style">
        <table class="grey-style">
          <tr>
            <th>
              <input
                type="checkbox"
                @click="select_all_via_check_box"
                v-model="all_select"
              />
            </th>
            <th>الكود</th>
            <th>اسم الطالب</th>
            <th>المخالفة</th>
            <th>عدد مرات الانذار</th>
            <th>العقوبة</th>
            <th>الحالة</th>
            <th>ملاحظات</th>
            <th>تعديل</th>
          </tr>
          <tr v-for="(infraction, index) in infractions" :key="index">
            <td>
              <input
                type="checkbox"
                v-model="updateItems"
                :value="infraction"
              />
            </td>
            <td>{{ index + 1 }}</td>
            <td>
              {{ infraction.student_first_name_ar }}
              {{ infraction.student_second_name_ar }}
            </td>
            <td>{{ infraction.infraction.name_ar }}</td>
            <td>{{ infraction.number_of_repeats }}</td>
            <td>-</td>
            <td>-</td>
            <td>{{ infraction.note }}</td>
            <td>
              <span class="blueBg"
                ><router-link
                  :to="{ name: 'createStudentInfractions', params: { id: infraction.id } }"
                  >تعديل</router-link
                ></span
              >
            </td>
          </tr>
          <tr v-if="emptyData()">
            <td colspan="8"><h4 class="text-center">لا توجد مخالفات مسجلة</h4></td>
          </tr>
        </table>

        <div class="tabel-btns-container save-btn" v-if="fullData()">
          <span class="blueBg"><router-link
                  :to="{ name: 'createStudentInfractions' }"
                  >إضافة مخالفة</router-link
                ></span>
          <span class="redBg" @click="deleteData">حذف</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store/index.js";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
  name: "studentInfractions",
  data() {
    return {
      all_select: false,
      infractions: [],
      updateItems: []
    };
  },
  mounted() {
    this.getTableResults();
  },
  methods: {
    getTableResults() {
      axios
        .get(store.state.domain + "/studentInfractions/index", config)
        .then(res => (this.infractions = res.data.data));
    },
    select_all_via_check_box() {
      if (this.all_select == false) {
        this.all_select = true;
        this.infractions.forEach(infraction => {
          this.updateItems.push(infraction.infraction.id, infraction.status);
        });
      } else {
        this.all_select = false;
        this.updateItems = [];
      }
    },
    emptyData() {
      return this.infractions.length < 1;
    },
    fullData() {
      return this.infractions.length >= 1;
    },
    deleteData() {
      const $this = this;
      this.updateItems.forEach(function(ids) {
        axios({
          method: "POST",
          url: store.state.domain + "/studentInfractions/removeStudentInfraction", 
          headers: {
            localization: store.state.localization
          }, 
          data:{
            student_id: ids.student_id,
            infraction_id: ids.infraction.id
          }
        }).then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.getTableResults();
            }
          });
      });
    }
  }
};
</script>
