<template>
  <section class="start-login-page">
    <div class="container">
      <div class="login-form boxedFormAllSteps">
        <img src="@/assets/Iskan-square.png" />
        <form>
          <div class="form-group">
            <input placeholder="اسم المستخدم" v-model="$v.form.email.$model" />
            <div v-if="$v.form.email.$error">
              <p v-if="!$v.form.email.required">This field is required</p>
              <p v-else-if="!$v.form.email.email">Not matched in Email format</p>
            </div>
          </div>
          <div class="form-group">
            <input
              type="Password"
              placeholder="كلمة المرور"
              v-model="$v.form.password.$model"
            />
            <div v-if="$v.form.password.$error">
              <p v-if="!$v.form.password.required">This field is required</p>
              <p v-else-if="!$v.form.password.minLength">
                Password must be more than 4 characters
              </p>
            </div>
          </div>
          <button
            type="submit"
            placeholder="Login"
            :disabled="$v.form.$invalid"
            @click="login"
          >
            دخول
          </button>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import { required, email, minLength } from "vuelidate/lib/validators";
import store from "../store/index.js";
export default {
  name: "login",
  data() {
    return {
      form: {
        email: "",
        password: ""
      }
    };
  },
  validations: {
    form: {
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(4)
      }
    }
  },
  methods: {
    login() {
      if (
        this.form.email === store.state.user &&
        this.form.password === store.state.password
      ) {
        this.$router.push("/dashboard");
      } else {
        console.log("Failed");
      }
    },
  }
};
</script>
