<template>
  <section class="terms-and-conditions">
    <div class="main-content-admin">
      <div class="boxedFormAllSteps">
        <AdmissionPhaseTabs v-if="!this.$route.params.id" tab="main" />
        <h4 class="text-center" v-if="this.$route.params.id">
          تعديل المرحلة
        </h4>
        <b-row
          class="data-row"
          v-if="
            !this.$route.params.id ||
              this.$route.name == 'editAdmissionPhaseTitle'
          "
        >
          <b-col md="12" lg="6" class="mt-md-3 data-item">
            <label for="">اسم المرحلة باللغة العربية <span class="required">*</span></label>
            <b-form-input
              type="text"
              v-model="titleAr"
              :state="TitleArState"
              required
            ></b-form-input>
            <b-form-invalid-feedback>
              ادخل اسم المرحلة باللغة العربية
            </b-form-invalid-feedback>
          </b-col>
          <b-col md="12" lg="6" class="mt-md-3  data-item">
            <label for="">اسم المرحلة باللغة الانجليزية <span class="required">*</span></label>
            <b-form-input
              type="text"
              v-model="titleEn"
              :state="TitleEnState"
              required
            ></b-form-input>
            <b-form-invalid-feedback>
              ادخل اسم المرحلة باللغة الانجليزية
            </b-form-invalid-feedback>
          </b-col>
        </b-row>
        <div
          class="head-page-main"
          v-if="
            this.$route.name == 'editAdmissionPhaseGender' ||
              !this.$route.params.id
          "
        >
          <div class="phaseGenders label-size">
            <p class="active-status" v-if="!this.$route.params.id">
              اختر النوع <span class="required">*</span>
            </p>
            <p
              class="active-status"
              v-if="this.$route.name == 'editAdmissionPhaseGender'"
            >
              تعديل نوع المتقدمين
            </p>
            <b-form-checkbox
              v-for="gender in genders"
              :key="gender.id"
              v-model="gendersSelectedID"
              :value="gender.id"
              >{{ gender.id == 1 ? "طالب" : "طالبة" }}</b-form-checkbox
            >
          </div>
        </div>
        <div>
          <b-row
            class="data-row"
            v-if="
              !this.$route.params.id ||
                this.$route.name == 'editAdmissionPhaseTime'
            "
          >
            <b-col md="12" lg="6" class="mt-md-3 data-item">
              <label for="" class="help-label"
                >تاريخ وتوقيت بداية المرحلة <span class="required">*</span>
              </label>
              <b-form-datepicker
                v-model="PhaseStartingDate"
                locale="ar"
                :min="currentDate"
                :initial-date="currentDate"
                v-bind="labels || {}"
                required
                :state="StartingDateState"
              ></b-form-datepicker>
              <b-form-timepicker
                locale="ar"
                v-model="PhaseStartingTime"
                v-bind="labels || {}"
                required
                :state="StartingTimeState"
              ></b-form-timepicker>
            </b-col>
            <b-col md="12" lg="6" class="mt-md-3 data-item">
              <label for="" class="help-label"
                >تاريخ وتوقيت انتهاء المرحلة <span class="required">*</span></label
              >
              <b-form-datepicker
                v-model="PhaseEndingDate"
                locale="ar"
                :min="PhaseStartingDate"
                v-bind="labels || {}"
                required
                :state="EndingDateState"
              ></b-form-datepicker>
              <b-form-timepicker
                locale="ar"
                v-model="PhaseEndingTime"
                v-bind="labels || {}"
                required
                :state="EndingTimeState"
              ></b-form-timepicker>
            </b-col>
          </b-row>
          <div
            class="admissionPhasePriorities"
            v-if="
              !this.$route.params.id ||
                this.$route.name == 'editAdmissionPhasePriorities'
            "
          >
            <label for="" class="help-label">اختر ورتب الأولويات </label>
            <div class="priorities-res">
              <div class="piorities-table">
                <table>
                  <tbody >
                    <tr v-for="(priority, index) in prioritiesList" :key="index">
                      <td>
                        <input
                          type="checkbox"
                          :value="priority.id"
                          v-model="checkedboxes"
                          :checked="!isChecked"
                          @change="updatePriority(index, priority.id)"
                        />
                        {{ priority.description_ar }}
                        <b-icon-chevron-up
                          @click="moveUp(index, priority.id)"
                        ></b-icon-chevron-up>
                        <b-icon-chevron-down
                          @click="moveDown(index, priority.id)"
                        ></b-icon-chevron-down>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <b-row v-if="this.err.length != 0">
            <ul class="form--error">
              <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
              <li
                v-if="
                  this.err.from_date ==
                    'Invalid date , please check previous assigned dates, there ara intersection between time intervals'
                "
              >
                تاريخ بداية المرحلة غير صالح، من فضلك تأكد من تواريخ المراحل
                السابقة حيث انه توجد مرحلة أخرى بنفس التاريخ
              </li>
              <li
                v-if="
                  this.err.to_date ==
                    'Invalid date , please check previous assigned dates, there ara intersection between time intervals'
                "
              >
                تاريخ نهاية المرحلة غير صالح، من فضلك تأكد من تواريخ المراحل
                السابقة حيث انه توجد مرحلة أخرى بنفس التاريخ
              </li>
            </ul>
          </b-row>
          <div class="save-term">
            <span
              v-if="
                this.TitleArState == true  &&
                this.TitleEnState == true &&
                this.$store.state.admissionPhase.genders.length > 0 &&
                this.$store.state.admissionPhase.phaseStartingDate &&
                  this.$store.state.admissionPhase.phaseEndingDate &&
                  this.$store.state.admissionPhase.phaseStartingTime &&
                  this.$store.state.admissionPhase.phaseEndingTime &&
                  !this.$route.params.id
              "
              @click="next()"
              >التالي</span
            >
            <span v-if="this.$route.params.id" @click="updatePhase">تعديل</span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import AdmissionPhaseTabs from "./AdmissionPhaseTabs";
import store from "@/store/index.js";
import axios from "axios";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "AdmissionPhaseForm",
  components: {
    AdmissionPhaseTabs
  },
  mounted() {
    if (
      !this.$route.params.id 
    ) {
      axios
        .get(store.state.domain + "/generalServices/listGenders", config)
        .then(res => (this.genders = res.data.data));
      axios
        .get(store.state.domain + "/housingPriorities/index", config)
        .then(
          res => (store.state.admissionPhase.prioritiesList = res.data.data)
        );
    } else if (this.$route.name == 'editAdmissionPhaseGender', config) {
      axios
        .get(store.state.domain + "/housingPriorities/index", config)
        .then(
          res => (store.state.admissionPhase.prioritiesList = res.data.data)
        );
    } else if (this.$route.name == "editAdmissionPhasePriorities", config) {
      axios
        .get(store.state.domain + "/generalServices/listGenders", config)
        .then(res => (this.genders = res.data.data));
      } else if (this.$route.name == "editAdmissionPhaseTitle", config) {
      axios
        .get(
          store.state.domain +
            "/admissionPhaseInterval/showIntervalById/" +
            this.$route.params.id, config
        )
        .then(res => {
          (store.state.admissionPhase.titleAr = res.data.data.title_ar),
            (store.state.admissionPhase.titleEn = res.data.data.title_en);
        });
    } else if (this.$route.name == "editAdmissionPhaseTime") {
      axios
        .get(
          store.state.domain +
            "/admissionPhaseInterval/showIntervalById/" +
            this.$route.params.id
        )
        .then(res => {
          (store.state.admissionPhase.PhaseStartingDate = res.data.data.start_time.slice(
            0,
            10
          )),
            (store.state.admissionPhase.PhaseEndingDate = res.data.data.end_time.slice(
              0,
              10
            ));
        });
    }
  },
  data() {
    return {
      err: [],
      genders: [],
      currentDate: new Date().toISOString().substring(0, 10),
      isActive: false,
      start_date: "",
      end_date: "",
      labels: {
        labelNoDateSelected: "لم يتم اختيار التاريخ",
        labelNoTimeSelected: "لم يتم اختيار الوقت",
        labelMinutes: "الدقائق",
        labelHours: "ساعات",
        labelSeconds: "ثواني",
        labelAmpm: "صباحا مساء",
        labelAm: "ص",
        labelPm: "م",
        labelCloseButton: "اختر"
      },
      isChecked: false,
      required: true,
      selected: false
    };
  },
  methods: {
    updatePhase() {
      if (this.$route.name == "editAdmissionPhaseGender") {
        axios({
          method: "POST",
          headers: {
            localization: store.state.localization
          },
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateAdmissionGender/" +
            this.$route.params.id,
          data: {
            gender_id: store.state.admissionPhase.genders
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase");
              store.state.admissionPhase.genders = [];
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else if (this.$route.name == "editAdmissionPhasePriorities") {
        axios({
          method: "POST",
          headers: {
            localization: store.state.localization
          },
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateAdmissionPriorities/" +
            this.$route.params.id,
          data: {
            priorities: store.state.admissionPhase.priorities
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase");
              store.state.admissionPhase.priorities = [];
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else if (this.$route.name == "editAdmissionPhaseTitle") {
        axios({
          method: "POST",
          headers: {
            localization: store.state.localization
          },
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateAdmissionTitle/" +
            this.$route.params.id,
          data: {
            title_ar: store.state.admissionPhase.titleAr,
            title_en: store.state.admissionPhase.titleEn
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase");
              (store.state.admissionPhase.titleAr = ""),
                (store.state.admissionPhase.titleEn = "");
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else {
        axios({
          method: "POST",
          headers: {
            localization: store.state.localization
          },
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateAdmissionTime/" +
            this.$route.params.id,
          data: {
            ...(store.state.admissionPhase.phaseStartingDate +
            " " +
            store.state.admissionPhase.phaseStartingTime
              ? {
                  start_time:
                    store.state.admissionPhase.phaseStartingDate +
                    " " +
                    store.state.admissionPhase.phaseStartingTime
                }
              : ""),
            ...(store.state.admissionPhase.phaseEndingDate +
            " " +
            store.state.admissionPhase.phaseEndingTime
              ? {
                  end_time:
                    store.state.admissionPhase.phaseEndingDate +
                    " " +
                    store.state.admissionPhase.phaseEndingTime
                }
              : "")
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase");
              (store.state.admissionPhase.phaseStartingDate = ""),
                (store.state.admissionPhase.phaseEndingDate = ""),
                (store.state.admissionPhase.phaseStartingTime = ""),
                (store.state.admissionPhase.phaseEndingTime = "");
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      }
    },
    next() {
      store.state.step++;
    },
    moveUp(rowIndex, id) {
      this.prioritiesList.splice(
        rowIndex - 1,
        0,
        this.prioritiesList.splice(rowIndex, 1)[0]
      );
      if (this.checkedboxes.length > 0) {
        store.commit('setPrioritiesCheckedboxesSplice', rowIndex - 1, 0, id),
        store.commit('setPrioritiesCheckedboxesSplice', rowIndex + 1, 1)
      }
    },
    moveDown(rowIndex, id) {
      this.prioritiesList.splice(
        rowIndex + 1,
        0,
        this.prioritiesList.splice(rowIndex, 1)[0]
      );
      if (this.checkedboxes.length > 0) {
        store.commit('setPrioritiesCheckedboxesSplice', rowIndex + 1, 0, id),
        store.commit('setPrioritiesCheckedboxesSplice', rowIndex, 1)
      }
    },
    updatePriority(index, id) {
      const indexCheckBox = this.checkedboxes.indexOf(id);
      if (!this.checkedboxes.includes(id)) {
        this.$store.dispatch("REMOVE_PRIORITY", 2);
      } else {
        store.commit("setPhasePriorities", {
          housing_priority_id: id,
          order: indexCheckBox + 1
        });
      }
    }
  },
  computed: {
    PhaseStartingDate: {
      get() {
        return this.$store.state.admissionPhase.phaseStartingDate;
      },
      set(value) {
        this.$store.commit("setPhaseStartingDate", value);
      }
    },
    PhaseStartingTime: {
      get() {
        return this.$store.state.admissionPhase.phaseStartingTime;
      },
      set(value) {
        this.$store.commit("setPhaseStartingTime", value);
      }
    },
    PhaseEndingDate: {
      get() {
        return this.$store.state.admissionPhase.phaseEndingDate;
      },
      set(value) {
        this.$store.commit("setPhaseEndingDate", value);
      }
    },
    PhaseEndingTime: {
      get() {
        return this.$store.state.admissionPhase.phaseEndingTime;
      },
      set(value) {
        this.$store.commit("setPhaseEndingTime", value);
      }
    },
    gendersSelectedID: {
      get() {
        return this.$store.state.admissionPhase.genders;
      },
      set(value) {
        this.$store.commit("setgendersSelectedID", value);
      }
    },
    checkedboxes: {
      get() {
        return this.$store.state.admissionPhase.prioritiesCheckedboxes;
      },
      set(value) {
        this.$store.commit("setPrioritiesCheckedboxesPush", value);
      }
    },
    titleAr: {
      get() {
        return this.$store.state.admissionPhase.titleAr;
      },
      set(value) {
        this.$store.commit("setPhaseTitleAr", value);
      }
    },
    titleEn: {
      get() {
        return this.$store.state.admissionPhase.titleEn;
      },
      set(value) {
        this.$store.commit("setPhaseTitleEn", value);
      }
    },
    prioritiesList: {
      get() {
        return this.$store.state.admissionPhase.prioritiesList;
      },
      set(value) {
        this.$store.commit("setPrioritiesList", value);
      }
    },
    TitleArState() {
      if (this.$store.state.admissionPhase.titleAr.length == 0) {
        return null;
      } else if (
        !this.$store.state.admissionPhase.titleAr.match(
          /[\u0600-\u06FF\u0750-\u077F]/
        )
      ) {
        return false;
      } else if (this.$store.state.admissionPhase.titleAr.length > 0) {
        return true;
      } else {
        return false;
      }
    },
    TitleEnState() {
      if (this.$store.state.admissionPhase.titleEn.length == 0) {
        return null;
      } else if (
        !this.$store.state.admissionPhase.titleEn.match(/^[a-zA-Z\s]*$/)
      ) {
        return false;
      } else if (this.$store.state.admissionPhase.titleEn.length > 0) {
        return true;
      } else {
        return false;
      }
    },
    StartingDateState() {
      if (this.$store.state.admissionPhase.phaseStartingDate.length == 0) {
        return null;
      } else if (
        this.$store.state.admissionPhase.phaseStartingDate.length > 9
      ) {
        return true;
      } else {
        return false;
      }
    },
    EndingDateState() {
      if (this.$store.state.admissionPhase.phaseEndingDate.length == 0) {
        return null;
      } else if (this.$store.state.admissionPhase.phaseEndingDate.length > 9) {
        return true;
      } else {
        return false;
      }
    },
    EndingTimeState() {
      if (this.$store.state.admissionPhase.phaseEndingTime.length == 0) {
        return null;
      } else if (this.$store.state.admissionPhase.phaseEndingTime.length > 5) {
        return true;
      } else {
        return false;
      }
    },
    StartingTimeState() {
      if (this.$store.state.admissionPhase.phaseStartingTime.length == 0) {
        return null;
      } else if (
        this.$store.state.admissionPhase.phaseStartingTime.length > 5
      ) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>
