<template>
  <div class="main-content-admin">
    <div class="d-flex addDorm">
          <span
            ><router-link to="dorms/createDorm">إضافة مدينة</router-link></span
          >
          <span
            ><router-link to="dorms/createGroup"
              >إضافة مجموعة</router-link
            ></span
          >
        </div>
      <div class="boxContianer">
        <b-row v-for="(category, i) in servicesCategory" :key="i" class="serviceCategory">
            <b-col xl="12">
                <h3><input type="checkbox" :value="category.id">{{category.name}}<b-icon-trash></b-icon-trash> <b-icon-pencil-fill></b-icon-pencil-fill></h3>
            </b-col>
            <slot class="subCategoryServ" v-for="(service, index) in services">
                <b-col xl="4" class="subCategoryServ" v-if="service.payment == category.id" :key="index">
                    <input type="checkbox" :value="service.id"><label>{{service.name}}</label><b-icon-trash></b-icon-trash>
                </b-col>
            </slot>
        </b-row>
      <div class="save-btn">
            <button
            type="submit"
            class="blueBg"
            >
            حفظ
            </button>
            <span class="redBg">
                حذف
            </span>
    </div>
  </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
    name: "services",
    data() {
        return {
            servicesCategory: "",
            services: ""
        }
    },
    mounted() {
      axios
        .get(store.state.domain + "/paymentFee", config)
        .then(res => (this.servicesCategory = res.data.data));
      axios
        .get(store.state.domain + "/paymentFeeDetails", config)
        .then(res => (this.services = res.data.data));
    }
}
</script>
