<template>
  <div class="main-content-admin">
    <Dormstabs tab="buildings" />
    <form>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>اختار المدينة</label>
          <b-form-select
            :class="building.dorm != null ? 'is-valid' : ''"
            v-model="building.dorm"
            disabled-field="notEnabled"
            required
          >
            <b-form-select-option :value="null" disabled
              >اختار المدينة</b-form-select-option
            >
            <b-form-select-option
              v-for="dorm in dorms"
              :key="dorm.id"
              :value="dorm.id"
              >{{ dorm.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5" class="data-item">
          <label>اختار المجموعة</label>
          <b-form-select
            :class="building.group != null ? 'is-valid' : ''"
            v-model="building.group"
            disabled-field="notEnabled"
            required
          >
            <b-form-select-option :value="null" disabled
              >اختار المجموعة</b-form-select-option
            >
            <b-form-select-option
              v-for="group in groups"
              :key="group.id"
              :value="group.id"
              >{{ group.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>اسم المبنى باللغة العربية</label>
          <b-form-input
          type="text"
          v-model="building.buildingNameAr"
          :state="BuildingArState"
          required
          ></b-form-input>
        <b-form-invalid-feedback>
            ادخل اسم المبنى باللغة العربية
        </b-form-invalid-feedback>
        </b-col>
        <b-col cols="5" class="data-item">
          <label>اسم المبنى باللغة الانجليزية</label>
          <b-form-input
          type="text"
          v-model="building.buildingNameEn"
          :state="BuildingEnState"
          required
          ></b-form-input>
        <b-form-invalid-feedback>
            ادخل اسم المبنى باللغة الانجليزية
        </b-form-invalid-feedback>
        </b-col>
      </b-row>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>نوع المقيمين</label>
          <b-form-select v-model="building.gender" disabled-field="notEnabled" :class="building.gender != null ? 'is-valid' : ''">
            <b-form-select-option :value="null" disabled required
              >نوع المقيمين</b-form-select-option
            >
            <b-form-select-option
              v-for="gender in genders"
              :key="gender.id"
              :value="gender.id"
              >{{
                gender.name_ar === "ذكر" ? "طلبة" : "طالبات"
              }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5">
          <label>الحالة</label>
          <b-form-select
            :class="building.status != null ? 'is-valid' : ''"
            v-model="building.status"
            :options="options"
            value-field="value"
            text-field="text"
            disabled-field="notEnabled"
            required
          >
            <b-form-select-option :value="null" disabled
              >اختار الحالة</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row v-if="err.length != 0">
        <ul class="form--error">
          <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
          <li
            v-if="
              this.err.building_group_id ==
                'The building group id field is required.'
            "
          >
            اختار المجموعة
          </li>
          <li v-if="this.err.dorm_id == 'The dorm id field is required.'">
            اختار المدينة
          </li>
          <li v-if="this.err.gender_id == 'The gender id field is required.'">
            اختار نوع المقيمين
          </li>
          <li v-if="this.err.name_ar == 'The name ar field is required.'">
            أدخل اسم المبنى باللغة العربية
          </li>
          <li v-if="this.err.name_en == 'The name en field is required.'">
            أدخل اسم المبنى باللغةالانجليزية
          </li>
          <li v-if="this.err.status == 'The status field is required.'">
            اختار حالة المبنى
          </li>
        </ul>
      </b-row>
      <div class="save-btn">
        <button
          type="submit"
          class="blueBg"
          @click.prevent="OnSubmit"
          v-if="recordForUpdate == null"
        >
          حفظ
        </button>
        <button
          type="submit"
          class="blueBg"
          @click.prevent="UpdateBuilding"
          v-if="recordForUpdate != null"
        >
          تعديل
        </button>
        <button
          type="submit"
          class="blueBg"
          @click.prevent="OnNext"
          v-if="recordForUpdate == null"
        >
          التالى
        </button>
        <span class="redBg" @click="resetData">إلغاء</span>
      </div>
    </form>
    <div class="show-data table-style">
      <table class="grey-style">
        <tr>
          <th>
            <input
              type="checkbox"
              @click="select_all_via_check_box"
              v-model="all_select"
            />
          </th>
          <th>اسم المبنى</th>
          <th>العنوان</th>
          <th>الأدوار المفعلة</th>
          <th>الغرف المفعلة</th>
          <th>الحالة</th>
          <th>نوع المقيم</th>
          <th>تعديل</th>
        </tr>
        <tr v-for="building in buildings" :key="building.id">
          <td>
            <input type="checkbox" v-model="updateItems" :value="building.id" />
          </td>
          <td>{{ building.name_ar }}</td>
          <td>{{ building.location_ar }}</td>
          <td>{{ building.floors_number }}</td>
          <td>{{ building.building_capacity }}</td>
          <td v-if="building.status == 'Active'" class="greenFont">مفعل</td>
          <td v-if="building.status != 'Active'" class="redFont">مغلق</td>
          <td>{{ building.gender.id == 1 ? "طلبه" : "طالبات" }}</td>
          <td>
            <span class="blueBg" @click="returnBuilding(building.id)"
              >تعديل</span
            >
          </td>
        </tr>
        <tr v-if="emptyData()">
          <td colspan="8"><h4 class="text-center">لا توجد مباني مسجلة</h4></td>
        </tr>
      </table>

      <div class="tabel-btns-container save-btn" v-if="fullData()">
        <span class="redBg" @click.prevent="disableStatus">تعليق</span>
        <span class="greenBg" @click.prevent="activeStatus">تفعيل</span>
        <span class="redBg" @click="deleteData">حذف</span>
      </div>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
import Dormstabs from "./DormsTabs";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "createBuilding",
  components: {
    Dormstabs
  },
  data() {
    return {
      err: [],
      recordForUpdate: null,
      all_select: false,
      updateItems: [],
      dorms: [],
      genders: "",
      checked: false,
      groups: "",
      buildings: [],
      building: {
        buildingNameAr: "",
        buildingNameEn: "",
        gender: null,
        group: null,
        dorm: null,
        status: null
      },
      options: [
        { value: "active", text: "مفعل" },
        { value: "off", text: "مغلق" }
      ]
    };
  },
  mounted() {
    this.getTableResults();
    axios
      .get(store.state.domain + "/dorm", config)
      .then(res => (this.dorms = res.data.data));
    axios
      .get(store.state.domain + "/buildingGroup", config)
      .then(res => (this.groups = res.data.data));
    axios
      .get(store.state.domain + "/generalServices/listGenders", config)
      .then(res => (this.genders = res.data.data));
  },
  computed: {
    BuildingArState() {
      if (this.building.buildingNameAr.length == 0) {
        return null; 
      } else if (!this.building.buildingNameAr.match(/[\u0600-\u06FF\u0750-\u077F]/)) {
        return false
      } else if (this.building.buildingNameAr.length > 0) {
        return true;
      } else {
        return false;
      }
    },
    BuildingEnState() {
      if (this.building.buildingNameEn.length == 0) {
        return null; 
      } else if (!this.building.buildingNameEn.match(/^[a-zA-Z\s]*$/)) {
        return false
      } else if (this.building.buildingNameEn.length > 0) {
        return true;
      } else {
        return false;
      }
    }
    },
  methods: {
    getTableResults() {
      axios
        .get(store.state.domain + "/dormBuilding", config)
        .then(res => (this.buildings = res.data.data));
    },
    resetData() {
      (this.building.buildingNameAr = ""),
        (this.building.buildingNameEn = ""),
        (this.building.gender = null),
        (this.building.group = null),
        (this.building.dorm = null),
        (this.building.status = null),
        (this.recordForUpdate = null);
    },
    returnBuilding(id) {
      this.recordForUpdate = id;
      axios.get(store.state.domain + "/dormBuilding/" + id, config).then(res => {
        (this.building.buildingNameAr = res.data.data.name_ar),
          (this.building.buildingNameEn = res.data.data.name_en),
          (this.building.status = res.data.data.status),
          (this.building.gender = res.data.data.gender.id),
          (this.building.dorm = res.data.data.dorm),
          (this.building.group = res.data.data.group.id);
        console.log(res.data.data);
      });
    },
    activeStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/dormBuilding/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "active"
          }
        }).then(res => {
        if (res.status == 200 || res.status == 201) {
          $this.getTableResults();
        }
      });
      });
      
    },
    disableStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/dormBuilding/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "off"
          }
        }).then(res => {
        if (res.status == 200 || res.status == 201) {
          $this.getTableResults();
        }
      }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    UpdateBuilding() {
      axios({
        method: "PUT",
        url: store.state.domain + "/dormBuilding/" + this.recordForUpdate,
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.building.buildingNameAr,
          name_en: this.building.buildingNameEn,
          status: this.building.status,
          dorm_id: this.building.dorm,
          building_group_id: this.building.group,
          gender_id: this.building.gender
        }
      }).then(res => {
        if (res.status == 200 || res.status == 201) {
          this.getTableResults();
        }
      }).catch(error => {
          this.err = error.response.data.errors;
      });
    },
    deleteData() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios.delete(store.state.domain + "/dormBuilding/" + id, { id: id }, config).then(res => {
        if (res.status == 200 || res.status == 201) {
          $this.getTableResults();
        }
      }).catch(error => {
          this.err = error.response.data.errors;
        });
      })
    },
    select_all_via_check_box() {
      if (this.all_select == false) {
        this.all_select = true;
        this.buildings.forEach(building => {
          this.updateItems.push(building.id);
        });
      } else {
        this.all_select = false;
        this.updateItems = [];
      }
    },
    OnSubmit() {
      axios({
        method: "POST",
        url: store.state.domain + "/dormBuilding",
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.building.buildingNameAr,
          name_en: this.building.buildingNameEn,
          status: this.building.status,
          dorm_id: this.building.dorm,
          building_group_id: this.building.group,
          gender_id: this.building.gender
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.getTableResults();
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    OnNext() {
      axios({
        method: "POST",
        url: store.state.domain + "/dormBuilding",
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.building.buildingNameAr,
          name_en: this.building.buildingNameEn,
          status: this.building.status,
          dorm_id: this.building.dorm,
          building_group_id: this.building.group,
          gender_id: this.building.gender
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.getTableResults();
            this.$router.push("/dorms/createFloor");
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    emptyData() { return this.buildings.length < 1 },
    fullData() { return this.buildings.length >= 1 }
  }
};
</script>
