<template>
  <div class="category-type">
    <div class="main-content-admin">
      <h2 class="text-center">أدوات المطبخ</h2>
      <div class="add-new-subject">
        <div class="add-container">
          <AddCategory addCategoryApi="/kitchenCategory" :reloadToolsCategoryResults="reloadToolsCategoryResults" />
        </div>
        <div class="add-container">
          <SubCategory
            addSubCategoryApi="/kitchenItem"
            addCategoryApi="/kitchenCategory"
            :reloadToolsResults="reloadToolsResults"
          />
        </div>
      </div>
      <div class="category-type" v-for="(tool, index) in tools" :key="index">
        <b-button class="coll-ing-btn" v-b-toggle="'collapse-' + index"
          ><font-awesome-icon icon="sort-down" />{{
            tool.name_ar
          }}<b-icon-trash @click="deleteMainCategory(tool.id)"></b-icon-trash>
          </b-button
        >
        <b-collapse visible :id="'collapse-' + index">
          <b-card> 
            <ul class="categories" v-if="kitchenItems.length >= 1">
              <slot v-for="(kitchenItem, index) in kitchenItems">
                <slot>
                  <li
                    :key="index"
                    v-if="tool.id == kitchenItem.kitchen_category_id.id"
                  >
                    {{ kitchenItem.name_ar }} <b-icon-trash @click="deleteItems(kitchenItem.id)"></b-icon-trash>
                  </li>
                </slot>
              </slot>
            </ul>
            <ul class="categories" v-else>
              <p>لا توجد أدوات</p>
            </ul>
          </b-card>
        </b-collapse>
      </div>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faBed, faSortDown } from "@fortawesome/free-solid-svg-icons";
import AddCategory from "../Ingredients/AddCategory";
import SubCategory from "../Ingredients/SubCategory";
library.add(faBed, faSortDown);
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "Tools",
  components: {
    AddCategory,
    SubCategory
  },
  data() {
    return {
      tools: [],
      kitchenItems: []
    };
  },
  mounted() {
    this.reloadToolsCategoryResults();
    this.reloadToolsResults();
  },
  methods: {
    reloadToolsCategoryResults() {
      axios
        .get(store.state.domain + "/kitchenCategory", config)
        .then(res => (this.tools = res.data.data));
    },
    reloadToolsResults() {
      axios
        .get(store.state.domain + "/kitchenItem", config)
        .then(res => (this.kitchenItems = res.data.data));
    },
    deleteMainCategory(id) {
      const $this = this; 
        axios
          .delete(store.state.domain + "/kitchenCategory/" + id, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.reloadToolsCategoryResults()
            }
        });
    },
    deleteItems(id) {
      const $this = this;
        axios
          .delete(store.state.domain + "/kitchenItem/" + id, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.reloadToolsResults();
            }
        });
    },
  }
};
</script>
