<template>
  <div class="main-content-admin">
    <h4 class="text-center">إضافة مخالفة</h4>
    <form>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>اسم المخالفة باللغة العربية</label>
          <b-form-input
            type="text"
            v-model="infraction.nameAr"
            required
          ></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم المخالفة باللغة الانجليزية
          </b-form-invalid-feedback>
        </b-col>
        <b-col cols="5" class="data-item">
          <label>اسم المخالفة باللغة الانجليزية</label>
          <b-form-input
            type="text"
            v-model="infraction.nameEn"
            required
          ></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم المخالفة باللغة الانجليزية
          </b-form-invalid-feedback>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>وصف المخالفة باللغة العربية</label>
          <b-form-input
            type="text"
            v-model="infraction.desAr"
            required
          ></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم المخالفة باللغة الانجليزية
          </b-form-invalid-feedback>
        </b-col>
        <b-col cols="5" class="data-item">
          <label>وصف المخالفة باللغة الانجليزية</label>
          <b-form-input
            type="text"
            v-model="infraction.desEn"
            required
          ></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم المخالفة باللغة الانجليزية
          </b-form-invalid-feedback>
        </b-col>
      </b-row>
      <b-row>
        <div class="table-style">
          <table class="grey-style">
            <label class="text-center">العقوبات</label>
            <tr>
              <th>مرات التكرار</th>
              <th>تصنيف العقوبة</th>
              <th>حذف</th>
            </tr>
            <tr v-for="(penalty,index) in repeatPenalties" :key="index">
              <td>{{index + 1}}</td>
              <td>
                <b-form-select
                  v-model="penalty.penalty_id"
                  disabled-field="notEnabled"
                >
            <b-form-select-option :value="null" disabled
              >اختار العقوبة</b-form-select-option
            >
            <b-form-select-option
              v-for="penalty in penalities"
              :key="penalty.id"
              :value="penalty.id"
              >{{ penalty.name_ar }}</b-form-select-option
            >
          </b-form-select>
              </td>
              <td><span class="redBg">حذف</span> </td>
            </tr>
            <tr>
              <td colspan="3"><span @click="addPenaltyRepeat">إضافة تكرار للعقوبة +</span></td>
            </tr>
          </table>
        </div>
      </b-row>
      <div class="save-btn">
        <button type="submit" class="blueBg" @click.prevent="OnSubmit">
          حفظ
        </button>
        <span class="redBg" @click="resetData">إلغاء</span>
      </div>
    </form>
  </div>
</template>

<script>
import store from "@/store/index.js";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
  name: "createInfraction",
  data() {
    return {
      err: [],
      penalities: [],
      repeatPenalties: [{
        repeat: 1,
        penalty_id: null
      }],
      infractions: [],
      infraction: {
        nameAr: "",
        nameEn: "",
        desAr: "",
        desEn: "",
        penalities: null
      },
    };
  },
  mounted() {
    if (this.$route.params.id) {
      axios
        .get(store.state.domain + "/infractions/show/" + this.$route.params.id, config)
        .then(res => {
          (this.infraction.nameAr = res.data.data.name_ar),
            (this.infraction.nameEn = res.data.data.name_en),
            (this.infraction.desAr = res.data.data.description_ar),
            (this.infraction.desEn = res.data.data.description_en);
        });
    };
    axios
      .get(store.state.domain + "/penalties/index", config)
      .then(res => (this.penalities = res.data.data));
  },
  methods: {
    addPenaltyRepeat(index) {
      this.repeatPenalties.push({
        repeat: "",
        penalty_id: index + 1
      })
    },
    OnSubmit: function() {
      if (this.$route.params.id) {
          axios({
          method: "PUT",
          url: store.state.domain + "/infractions/update/" + this.$route.params.id,
          headers: {
            localization: store.state.localization
          },
          data: {
            name_ar: this.infraction.nameAr,
            name_en: this.infraction.nameEn,
            description_ar: this.infraction.desAr,
            description_en: this.infraction.desEn
          }
        });
      } else {
        axios({
        method: "POST",
        url: store.state.domain + "/infractions/store",
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.infraction.nameAr,
          name_en: this.infraction.nameEn,
          description_ar: this.infraction.desAr,
          description_en: this.infraction.desEn
        }
      }).then( 
            axios
              .get(store.state.domain + "/infractions", config)
                .then(res => (this.infractions = res.data.data)).then(
                  axios({
                    method: "POST",
                    url: store.state.domain + "/repeatedInfractionPenalties/updateOrCreate",
                    headers: {
                      localization: store.state.localization
                    },
                    data: {
                      "repeated_infraction_penalty":[
                            {
                                infraction_id: this.infractions[this.infractions.length - 1].id,
                                repeat_penalty: this.repeatPenalties
                            }
                        ]
                    }
                  })
                )
        )
      }
    },
    resetData() {
      this.infraction.nameAr = "";
      this.infraction.nameEn = "";
      this.infraction.desAr = "";
      this.infraction.desEn = null;
    },
  }
};
</script>
