<template>
  <section class="datatables-admin-pages">
    <div class="main-content-admin">
      <div class="top-charts">
        <div v-b-modal.modal-3 class="chart">
          <AdminstrationApplicantChart />
        </div>
        <div v-b-modal.modal-4 class="chart">
          <AdminstrationCitiesChart />
        </div>
      </div>
      <div class="boxContianer">
        <div class="d-flex addDorm">
          <span
            ><router-link to="dorms/createDorm">إضافة مدينة</router-link></span
          >
          <span
            ><router-link to="dorms/createGroup"
              >إضافة مجموعة</router-link
            ></span
          >
          <span
            ><router-link to="dorms/createBuilding"
              >إضافة مبنى</router-link
            ></span
          >
          <span
            ><router-link to="dorms/createFloor">إضافة دور</router-link></span
          >
          <span
            ><router-link to="dorms/createPartition"
              >إضافة غرفة</router-link
            ></span
          >
        </div>
      </div>
      <div class="boxContianer">
        <div class="table-style">
          <table class="grey-style">
            <tr>
              <th>
                <input
                  type="checkbox"
                  @click="select_all_via_check_box"
                  v-model="all_select"
                />
              </th>
              <th>المدينة</th>
              <th>العنوان</th>
              <th>المباني المفعلة</th>
              <th>الغرف المفعلة</th>
              <th>نسبة الاشغال</th>
              <th>الحالة</th>
              <th>تعديل</th>
            </tr>
            <tr v-for="dorm in dorms" :key="dorm.id">
              <td>
                <input type="checkbox" v-model="updateItems" :value="dorm.id" />
              </td>
              <td>{{ dorm.name_ar }}</td>
              <td>{{ dorm.address_ar }}</td>
              <td v-if="dorm.total_buildings_count > 0">{{ dorm.total_active_buildings_count}} / {{dorm.total_buildings_count}}</td>
              <td v-else>لا توجد مباني</td>
              <td v-if="dorm.total_partitions_count > 0">{{ dorm.total_active_partitions_count}} / {{dorm.total_partitions_count}}</td>
              <td v-else>لا توجد غرف</td>
              <td v-if="dorm.total_partitions_count > 0">{{ (dorm.total_active_partitions_count / dorm.total_partitions_count)*100 }} % </td>
              <td v-else>لا يوجد إشغال</td>
              <td v-if="dorm.status == 'Active'" class="greenFont">مفعل</td>
              <td v-if="dorm.status != 'Active'" class="redFont">مغلق</td>
              <td>
                <span class="blueBg"
                  ><router-link
                    :to="{ name: 'editDorm', params: { id: dorm.id } }"
                    >تعديل</router-link
                  ></span
                >
              </td>
            </tr>
            <tr v-if="emptyData()">
              <td colspan="8">
                <h4 class="text-center">لا توجد مدن مسجلة</h4>
              </td>
            </tr>
          </table>

          <div class="tabel-btns-container save-btn" v-if="fullData()">
            <span class="redBg" @click="deleteData">حذف</span>
            <span class="redBg" @click="disableStatus">تعليق</span>
            <span class="greenBg" @click="activeStatus">تفعيل</span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import store from "@/store/index.js";
import axios from "axios";
import AdminstrationCitiesChart from "../Charts/AdminstrationCitiesChart";
import AdminstrationApplicantChart from "../Charts/AdminstrationApplicantChart";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "CurrentDorms",
  components: {
    AdminstrationCitiesChart,
    AdminstrationApplicantChart
  },
  data() {
    return {
      dorms: [],
      buildings: [],
      checked: false,
      dormsIds: [],
      all_select: false,
      updateItems: []
    };
  },
  mounted() {
    this.getTableResults();
  },
  methods: {
    SaveDataStore() {
      this.dorms.name_ar = store.state.editDorm.dormNameAr;
      this.dorms.name_en = store.state.editDorm.dormNameEn;
      this.dorms.address_ar = store.state.editDorm.dormAddress;
      this.dorms.status = store.state.editDorm.dormStatus;
    },
    deleteData() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios
          .delete(store.state.domain + "/dorm/" + id, { id: id }, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.getTableResults();
            }
          });
      });
    },
    activeStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/dorm/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "active"
          }
        }).then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        });
      });
      this.getTableResults;
    },
    disableStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/dorm/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "off"
          }
        }).then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        });
      });
      this.getTableResults;
    },
    select_all_via_check_box() {
      if (this.all_select == false) {
        this.all_select = true;
        this.dorms.forEach(dorm => {
          this.updateItems.push(dorm.id, dorm.status);
        });
      } else {
        this.all_select = false;
        this.updateItems = [];
      }
    },
    getTableResults() {
      axios
        .get(store.state.domain + "/dorm", config)
        .then(res => (this.dorms = res.data.data));
    },
    emptyData() {
      return this.dorms.length < 1;
    },
    fullData() {
      return this.dorms.length >= 1;
    }
  }
};
</script>
