<template>
  <section class="terms-and-conditions">
    <div class="main-content-admin">
      <div class="boxedFormAllSteps">
        <AdmissionPhaseTabs v-if="!this.$route.params.id" tab="terms" />
        <h4
          class="text-center"
          v-if="
            this.$route.params.id &&
              this.$route.name != 'addRegistrationInterval'
          "
        >
          تعديل فترة القبول <span class="required">*</span>
        </h4>
        <h4
          class="text-center"
          v-if="
            this.$route.params.id &&
              this.$route.name == 'addRegistrationInterval'
          "
        >
          إضافة فترة قبول
        </h4>
        <div
          v-for="(interval, index) in intervals"
          :key="index"
          class="admissionIntervals"
        >
          <h3 class="text-center" v-if="!$route.params.id">
            الفترة رقم {{ index + 1 }}
          </h3>
          <b-row
            class="data-row"
            v-if="
              !$route.params.id ||
                $route.name == 'editRegistrationIntervalSeniority' ||
                $route.name == 'addRegistrationInterval'
            "
          >
            <p class="active-status">اختر فئة المقبولين <span class="required">*</span></p>
            <b-form-checkbox
              v-for="senior in seniors"
              :key="senior.id"
              v-model="interval.student_seniority_id"
              :value="senior.id"
              >{{
                senior.id == 1 ? "طلبة قدامى" : "طلبة مستجدين"
              }}</b-form-checkbox
            >
          </b-row>
          <b-row
            class="data-row"
            v-if="
              !$route.params.id ||
                $route.name == 'editRegistrationIntervalTime' ||
                $route.name == 'addRegistrationInterval'
            "
          >
            <b-col cols="6" class="data-item">
              <label for="" class="help-label"
                >تاريخ وتوقيت بداية فترة التسجيل <span class="required">*</span></label
              >
              <b-form-datepicker
                :class="intervalStartingDate[index] ? 'is-valid' : ''"
                @input="addStartingDate(index)"
                v-model="intervalStartingDate[index]"
                locale="ar"
                :min="PhaseStartingDate || currentDate"
                :max="PhaseEndingDate"
                :initial-date="currentDate"
                v-bind="labels || {}"
                aria-required="true"
                :required="required"
              ></b-form-datepicker>
              <b-form-timepicker
               :class="intervalStartingTime[index] ? 'is-valid' : ''"
                @input="addStartingDate(index)"
                v-model="intervalStartingTime[index]"
                locale="ar"
                v-bind="labels || {}"
                aria-required="true"
                :required="required"
              ></b-form-timepicker>
            </b-col>
            <b-col cols="6" class="data-item">
              <label for="" class="help-label"
                >تاريخ وتوقيت انتهاء فترة التسجيل <span class="required">*</span></label
              >
              <b-form-datepicker
               :class="intervalEndingDate[index] ? 'is-valid' : ''"
                @input="addEndingDate(index)"
                v-model="intervalEndingDate[index]"
                locale="ar"
                :min="intervalStartingDate[index] || currentDate"
                :max="PhaseEndingDate"
                v-bind="labels || {}"
                aria-required="true"
                :required="required"
              ></b-form-datepicker>
              <b-form-timepicker
                :class="intervalEndingTime[index] ? 'is-valid' : ''"
                @input="addEndingDate(index)"
                v-model="intervalEndingTime[index]"
                locale="ar"
                v-bind="labels || {}"
                aria-required="true"
                :required="required"
              ></b-form-timepicker>
            </b-col>
          </b-row>
          <table
            v-if="
              !$route.params.id ||
                $route.name == 'addRegistrationInterval' ||
                $route.name == 'editRegistrationIntervalConditions'
            "
          >
            <tr v-for="condition in conditions" :key="condition.id">
              <td>
                <input
                  type="checkbox"
                  @change="ifChecked(condition.id, index)"
                  :value="condition.id"
                />
                <span>{{ condition.description_ar }}</span>
                <div v-if="condition.id == 9">
                  <b-form-checkbox
                    v-for="govern in governs"
                    v-model="
                      interval.applying_conditions.accepted_governorates_id
                    "
                    :key="govern.id"
                    :value="govern.id"
                    >{{ govern.Governorate_ar }}</b-form-checkbox
                  >
                </div>
                <div v-if="condition.id == 10">
                  <b-form-checkbox
                    v-for="grade in grades"
                    v-model="interval.applying_conditions.accepted_grades_id"
                    :key="grade.id"
                    :value="grade.id"
                    >{{ grade.grade_name_ar }}</b-form-checkbox
                  >
                </div>
                <div v-if="condition.id == 11">
                  <b-form-checkbox
                    v-for="faculty in faculties"
                    v-model="interval.applying_conditions.accepted_faculties_id"
                    :key="faculty.id"
                    :value="faculty.id"
                    >{{ faculty.faculty_name_ar }}</b-form-checkbox
                  >
                </div>
              </td>
              <td @click="selectBefore(condition.id, $event, index)">
                شرط مسبق
              </td>
              <td @click="selectAfter(condition.id, $event, index)">
                شرط لاحق
              </td>
            </tr>
          </table>

          <div
            class="caption-terms"
            v-if="
              !$route.params.id ||
                $route.name == 'editRegistrationIntervalConditions'
            "
          >
            <h3>ملحوظه</h3>
            <p class="cap-item">
              -تعنى الشروط المسبقه ان هذا الشرط سيتم تطبيقه فى تاريخ البدء
            </p>
            <p class="cap-item">
              -تعنى الشروط اللاحقه ان هذا الشرط سيتم تطبيقه عند استلام طلب
              الطالب
            </p>
          </div>
        </div>
        <b-row v-if="this.err != 0">
          <ol class="form--error">
            <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
                <li v-for="(error, i) in err.registration_interval" :key="i">{{error}}</li>

          </ol>
        </b-row>
        <div class="save-btn">
          <span @click="prev" class="blueBg" v-if="!this.$route.params.id"
            >السابق</span
          >
          <span
            class="blueBg"
            @click="addNewInterval"
            v-if="!this.$route.params.id"
            >إضافة فترة تسجيل</span
          >
          <button
            type="submit"
            @click.prevent="OnSubmit"
            class="greenBg"
            v-if="!this.$route.params.id"
          >
            انشاء
          </button>
          <span class="blueBg" @click.prevent="updateInterval" v-if="this.$route.params.id">تعديل</span>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import AdmissionPhaseTabs from "./AdmissionPhaseTabs";
import store from "@/store/index.js";
import axios from "axios";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  components: {
    AdmissionPhaseTabs
  },
  name: "AdmissionPhaseInterval",
  mounted() {
    this.intervals = store.state.admissionPhase.intervals;
    axios
      .get(store.state.domain + "/dormsRegulationCondition/list", config)
      .then(res => {
        this.conditions = res.data;
        const lengthArray = Object.keys(res.data.data).length;
        this.conditions = res.data.data;
        for (let i = 0; i < lengthArray; i++) {
          this.conditions[i];
        }
      });
    axios
      .get(store.state.domain + "/generalServices/listGovernorates", config)
      .then(res => (this.governs = res.data.data));
    axios
      .get(store.state.domain + "/facultiesSelection/listAllFaculties", config)
      .then(res => (this.faculties = res.data.data));
    axios
      .get(store.state.domain + "/generalServices/listStudentSeniority", config)
      .then(res => (this.seniors = res.data.data));
    axios
      .get(store.state.domain + "/equivalentRate/index", config)
      .then(res => (this.grades = res.data.data));
    if (this.$route.params.id) {
      axios
        .get(
          store.state.domain +
            "/registrationInterval/show/" +
            this.$route.params.id, config
        )
        .then(
          res =>
            (this.intervals.student_seniority_id =
              res.data.data.student_seniority)
        );
    }
  },
  data() {
    return {
      err: {},
      seniors: "",
      governs: "",
      grades: "",
      faculties: "",
      conIDs: [],
      conditonid: null,
      conditions: null,
      RegulationConditionsBefore: [],
      RegulationConditionsAfter: [],
      RegulationConditions: [],
      currentDate: new Date().toISOString().substring(0, 10),
      intervals: [
        {
          start_time: "",
          end_time: "",
          student_seniority_id: [],
          applying_conditions: {
            regulation_conditions: [],
            accepted_governorates_id: [],
            accepted_grades_id: [],
            accepted_faculties_id: []
          }
        }
      ],
      labels: {
        labelNoDateSelected: "لم يتم اختيار التاريخ",
        labelNoTimeSelected: "لم يتم اختيار الوقت",
        labelMinutes: "الدقائق",
        labelHours: "ساعات",
        labelSeconds: "ثواني",
        labelAmpm: "صباحا مساء",
        labelAm: "ص",
        labelPm: "م",
        labelCloseButton: "اختر"
      },
      required: true,
      selected: false,
      indeterminate: false
    };
  },
  computed: {
    PhaseStartingDate: {
      get() {
        return this.$store.state.admissionPhase.phaseStartingDate;
      },
      set(value) {
        this.$store.commit("setPhaseStartingDate", value);
      }
    },
    PhaseEndingDate: {
      get() {
        return this.$store.state.admissionPhase.phaseEndingDate;
      },
      set(value) {
        this.$store.commit("setPhaseEndingDate", value);
      }
    },
    intervalEndingDate: {
      get() {
        return this.$store.state.admissionPhase.intervalEndingDate;
      },
      set(value) {
        this.$store.push("setintervalEndingDate", value);
      }
    },
    intervalStartingDate: {
      get() {
        return this.$store.state.admissionPhase.intervalStartingDate
      },
      set(value) {
        this.$store.push("setintervalStartingDate", value);
      }
    },
    intervalEndingTime: {
      get() {
        return this.$store.state.admissionPhase.intervalEndingTime;
      },
      set(value) {
        this.$store.push("setintervalEndingTime", value);
      }
    },
    intervalStartingTime: {
      get() {
        return this.$store.state.admissionPhase.intervalStartingTime
      },
      set(value) {
        this.$store.push("setintervalStartingTime", value);
      }
    }
  },
  methods: {
    addStartingDate(index) {
      this.intervals[index].start_time =
        this.$store.state.admissionPhase.intervalStartingDate[index] + " " + this.$store.state.admissionPhase.intervalStartingTime[index];
    },
    addEndingDate(index) {
      this.intervals[index].end_time =
        this.$store.state.admissionPhase.intervalEndingDate[index] + " " + this.$store.state.admissionPhase.intervalEndingTime[index];
    },
    ifChecked(id, index) {
      this.intervals[index].applying_conditions.regulation_conditions.push({
        dorms_regulation_condition_id: id,
        pre_applying: 1
      });
    },
    updateInterval() {
      if (this.$route.name == "editRegistrationIntervalTime") {
        axios({
          method: "POST",
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateRegistrationIntervalTime/" +
            this.$route.params.id,
          headers: {
            localization: store.state.localization
          },
          data: {
            start_time: this.intervals[0].start_time,
            end_time: this.intervals[0].end_time
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase"), this.prev();
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else if (this.$route.name == "editRegistrationIntervalSeniority") {
        axios({
          method: "POST",
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateRegistrationIntervalSeniority/" +
            this.$route.params.id,
          headers: {
            localization: store.state.localization
          },
          data: {
            student_seniority_id: this.intervals[0].student_seniority_id
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase"), this.prev();
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else if (this.$route.name == "addRegistrationInterval") {
        axios({
          method: "POST",
          url:
            store.state.domain +
            "/admissionPhaseInterval/addRegistrationInterval/" +
            this.$route.params.id,
          headers: {
            localization: store.state.localization
          },
          data: {
            start_time: this.intervals[0].start_time,
            end_time: this.intervals[0].end_time,
            student_seniority_id: this.intervals[0].student_seniority_id,
            applying_conditions: this.intervals[0].applying_conditions
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase"), this.prev();
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else if (this.$route.name == "editRegistrationIntervalConditions") {
        axios({
          method: "POST",
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateRegistrationIntervalConditions/" +
            this.$route.params.id,
          headers: {
            localization: store.state.localization
          },
          data: {
            applying_conditions: this.intervals[0].applying_conditions,
            ...(this.intervals[0].accepted_grades_id
              ? { accepted_grades_id: this.intervals[0].accepted_grades_id }
              : null),
            ...(this.intervals[0].applying_conditions.accepted_governorates_id
              ? {
                  accepted_governorates_id: this.intervals[0]
                    .applying_conditions.accepted_governorates_id
                }
              : null),
            ...(this.intervals[0].accepted_faculties_id
              ? {
                  accepted_faculties_id: this.intervals[0].accepted_faculties_id
                }
              : null)
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase"), this.prev();
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      } else {
        axios({
          method: "POST",
          url:
            store.state.domain +
            "/admissionPhaseInterval/updateRegistrationIntervalSeniority/" +
            this.$route.params.id,
          headers: {
            localization: store.state.localization
          },
          data: {
            start_time: this.intervals[0].start_time,
            end_time: this.intervals[0].end_time
          }
        })
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              this.$router.push("/admissionPhase"), this.prev();
            }
          })
          .catch(error => {
            this.err = error.response.data.errors;
          });
      }
    },
    addNewInterval() {
      this.intervals.push({
        start_time: "",
        end_time: "",
        student_seniority_id: [],
        applying_conditions: {
          regulation_conditions: [],
          accepted_governorates_id: [],
          accepted_grades_id: [],
          accepted_faculties_id: [],
        }
      });
    },
    mountPreApply(id) {
      this.conditonid = id;
      let indexAfter = this.intervals.RegulationConditionsAfter.indexOf(id);
      if (indexAfter >= 0) {
        this.intervals.RegulationConditionsAfter.splice(indexAfter, 1);
        this.intervals.RegulationConditionsBefore.push(id);
      } else {
        this.intervals.RegulationConditionsBefore.push(id);
      }
    },
    selectBefore(id, event, index) {
      this.conditonid = id;
      let indexAfter = this.intervals[index].RegulationConditionsAfter.indexOf(
        id
      );
      if (indexAfter >= 0) {
        this.intervals[index].RegulationConditionsAfter.splice(indexAfter, 1);
        this.intervals[index].RegulationConditionsBefore.push(id);
      } else {
        this.intervals[index].RegulationConditionsBefore.push(id);
      }
      event.target.nextElementSibling.classList.remove("active");
      event.target.classList.toggle("active");
    },
    selectAfter(id, event, index) {
      this.conditonid = id;
      let indexBefore = this.intervals[
        index
      ].RegulationConditionsBefore.indexOf(id);
      if (indexBefore >= 0) {
        this.intervals[index].RegulationConditionsBefore.splice(indexBefore, 1);
        this.intervals[index].RegulationConditionsAfter.push(id);
      } else {
        this.intervals[index].RegulationConditionsAfter.push(id);
      }
      event.target.previousElementSibling.classList.remove("active");
      event.target.classList.toggle("active");
    },
    OnSubmit() {
      axios({
        method: "POST",
        url: store.state.domain + "/admissionPhaseInterval/create",
        headers: {
          localization: store.state.localization
        },
        data: {
          start_time:
            store.state.admissionPhase.phaseStartingDate +
            " " +
            store.state.admissionPhase.phaseStartingTime,
          end_time:
            store.state.admissionPhase.phaseEndingDate +
            " " +
            store.state.admissionPhase.phaseEndingTime,
          priorities: store.state.admissionPhase.priorities,
          gender_id: store.state.admissionPhase.genders,
          title_ar: store.state.admissionPhase.titleAr,
          title_en: store.state.admissionPhase.titleEn,
          registration_interval: this.intervals
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.$router.push("/admissionPhase"), this.prev();
            store.state.admissionPhase.phaseStartingDate = "",
            store.state.admissionPhase.phaseStartingTime = "",
            store.state.admissionPhase.phaseEndingDate = "",
            store.state.admissionPhase.phaseEndingTime = "",
            store.state.admissionPhase.priorities = [],
            store.state.admissionPhase.genders = [],
            store.state.admissionPhase.titleAr = "",
            store.state.admissionPhase.titleEn = ""
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    prev() {
      store.state.step--;
      store.commit('setIntervals', this.intervals)
    }
  }
};
</script>
<style lang="scss" scoped>
.terms-and-conditions {
  .save-btn {
    float: unset !important;
    text-align: left;
  }
  .admissionIntervals {
    margin-top: 20px;
    h3 {
      font-size: 25px;
    }
  }
  .data-item {
    margin: 20px auto;
    .b-form-datepicker {
      margin: 5px 0;
    }
  }
  table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 65px;
    tr {
      background: #fff;
      width: 75%;
    }
    td:first-child {
      width: 71%;
    }
    td,
    th {
      border: 1px solid #c4c4c4;
      padding: 8px 15px 8px 15px;
      font-weight: bold;
      color: #5c5c5c;
      input {
        vertical-align: inherit;
        margin-left: 10px;
      }
    }
    .active {
      // to active the term as before or after condition
      background: #00bbf3;
      color: #fff;
    }
  }
  .caption-terms {
    padding-top: 25px;
    h3 {
      font-size: 16px;
      font-weight: bold;
      color: #5c5c5c;
    }
    p {
      font-size: 14px;
      color: #5c5c5c;
    }
  }
  .save-term {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    span {
      background: #008eb9;
      padding: 4px 25px;
      border-radius: 10px;
      color: #fff;
      cursor: pointer;
    }
  }
}
</style>