<template>
  <div class="main-content-admin">
    <h2 class="text-center">إضافة جدول</h2>
    <form>
      <b-row>
        <b-col xl="6">
          <label>الاسم باللغة العربية</label>
          <b-form-input
            type="text"
            required
            v-model="tableAr"
            :state="tableArState"
          ></b-form-input>
        </b-col>
        <b-col xl="6">
          <label>الاسم باللغة الانجليزية</label>
          <b-form-input
            type="text"
            required
            v-model="tableEn"
            :state="tableEnState"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <label>تاريخ البدء</label>
          <b-form-datepicker
            v-model="startDate"
            locale="ar"
            :min="currentDate"
            :initial-date="currentDate"
            v-bind="labels || {}"
            required
            :state="StartingDateState"
          ></b-form-datepicker>
        </b-col>
        <b-col>
          <label>تاريخ الانتهاء</label>
          <b-form-datepicker
            v-model="endDate"
            locale="ar"
            :min="startDate"
            v-bind="labels || {}"
            required
            :state="EndingDateState"
          ></b-form-datepicker>
        </b-col>
      </b-row>
      <b-row>
        <b-col
          xl="12"
          md="12"
          class="mb-3"
          v-for="(day, index) in days"
          :key="index"
        >
          <b-button class="coll-ing-btn" v-b-toggle="'collapse-' + index">
            <font-awesome-icon icon="sort-down" />
            <span>{{ day.dayName }} </span>
          </b-button>
          <b-collapse :id="'collapse-' + index">
            <b-row>
              <b-col xl="4" v-for="meal in meals" :key="meal.id">
                <b-form-checkbox name="check-button" switch v-model="selected" :value="meal.name_ar + meal.id">{{ meal.name_ar }}</b-form-checkbox>
                <slot>
                  <slot v-for="dish in dishes">
                    <b-form-checkbox v-if="meal.id == dish.meal.meal_type_id" name="check-button" v-model="selected" :value="dish.meal.id + dish.id">{{dish.meal.name_ar}}</b-form-checkbox>
                  </slot>
                </slot>
<!--              <b-form-input
                  type="text"
                  v-model="search"
                  @input="onChange(index)"
                  @keyup.down="onArrowDown"
                  @keyup.up="onArrowUp"
                  @keyup.enter="onEnter"
                ></b-form-input>
                <ul

                  v-show="isOpen"
                  class="autocomplete-results"
                >
                  <li
                    v-for="(ingrediant, i) in ingredients"
                    :key="i"
                    @click="setResult(ingrediant.name_ar)"
                    class="autocomplete-result"
                    :class="{ 'is-active': i === arrowCounter }"
                  >
                    {{ ingrediant.name_ar }}
                  </li> 
                </ul> -->
              </b-col>
            </b-row>
          </b-collapse>
        </b-col>
      </b-row>
    </form>
    <div class="save-btn">
      <button type="submit" class="blueBg" @click="OnSubmit">
        حفظ
      </button>
      <span class="redBg">الغاء</span>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
  name: "createTimeTable",
  data() {
    return {
      tableAr: "",
      tableEn: "",
      startDate: "",
      endDate: "",
      search: "",
      selected: "",
      checked: false,
      isOpen: false,
      meals: [],
      dishes: [],
      arrowCounter: 0,
      results: [],
      ingredients: [],
      currentDate: new Date().toISOString().substring(0, 10),
      labels: {
        labelNoDateSelected: "لم يتم اختيار التاريخ",
        labelNoTimeSelected: "لم يتم اختيار الوقت",
        labelMinutes: "الدقائق",
        labelHours: "ساعات",
        labelSeconds: "ثواني",
        labelAmpm: "صباحا مساء",
        labelAm: "ص",
        labelPm: "م",
        labelCloseButton: "اختار"
      },
      days: [
        { dayName: "السبت" },
        { dayName: "الأحد" },
        { dayName: "الأثنين" },
        { dayName: "الثلاثاء" },
        { dayName: "الاربعاء" },
        { dayName: "الخميس" },
        { dayName: "الجمعة" }
      ]
    };
  },
  mounted() {
    axios
      .get(store.state.domain + "/mealtype", config)
      .then(res => (this.meals = res.data.data));
    axios
      .get(store.state.domain + "/meal", config)
      .then(res => (this.ingredients = res.data.data));
    axios
      .get(store.state.domain + "/mealIngredient", config)
      .then(res => (this.dishes = res.data.data));
  },
  computed: {
    StartingDateState() {
      if (this.startDate.length == 0) {
        return null;
      } else if (this.startDate.length > 9) {
        return true;
      } else {
        return false;
      }
    },
    EndingDateState() {
      if (this.endDate.length == 0) {
        return null;
      } else if (this.endDate.length > 9) {
        return true;
      } else {
        return false;
      }
    },
    tableArState() {
      if (this.tableAr.length == 0) {
        return null;
      } else if (!this.tableAr.match(/[\u0600-\u06FF\u0750-\u077F]/)) {
        return false;
      } else if (this.tableAr.length > 3) {
        return true;
      } else {
        return false;
      }
    },
    tableEnState() {
      if (this.tableEn.length == 0) {
        return null;
      } else if (!this.tableEn.match(/^[a-zA-Z\s]*$/)) {
        return false;
      } else if (this.tableEn.length > 3) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    OnSubmit() {
      axios({
        method: "POST",
        url: store.state.domain + "/mealTime",
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.tableAr,
          name_en: this.tableEn,
          start_date: this.startDate,
          end_date: this.endDate,
          day: ["Sunday", "Monday", "Tuesday"],
          category_id: [14,16,18,20,1],
          meal_id: [2,4,6,1,1]
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.$router.push("/feeding/timeTable");
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    onChange(index) {
      this.$emit("input", this.search[index]);
      this.filterResults();
      this.isOpen = true;
    },
    filterResults() {
      this.results = this.ingredients.filter(ingredient => {
        return ingredient.name_ar.indexOf(this.search) > -1;
      });
    },
    setResult(result) {
      this.selected.push(result);
      this.isOpen = false;
      this.search = result;
    },
    onArrowDown() {
      if (this.arrowCounter < this.results.length) {
        this.arrowCounter = this.arrowCounter + 1;
      }
    },
    onArrowUp() {
      if (this.arrowCounter > 0) {
        this.arrowCounter = this.arrowCounter - 1;
      }
    },
    onEnter() {
      this.search = this.results[this.arrowCounter];
      this.isOpen = false;
      this.arrowCounter = -1;
    }
  }
};
</script>
