  position: relative;
  width: 130px;
}
.autocomplete-results {
  padding: 0;
  margin: 0;
  border: 1px solid #eeeeee;
  overflow: auto;
  width: 100%;
  background: #fff;
  .autocomplete-result {
    margin-top: 2px;
    list-style: none;
    padding: 10px 15px;
    cursor: pointer;
  }
}
.autocomplete-result.is-active,
.autocomplete-result:hover {
  background-color: #008eb9;
  color: white;
}
.custom-control-label::before, .custom-control-label::after {right:auto}
.custom-switch .custom-control-label::before {right:0}
.custom-switch .custom-control-label::after {right: calc(0.75rem + 2px); left: auto}
