<template>
    <div class="main-content-admin">
        <h3 class="text-center">مراحل التقديم</h3>
         
        <div class="boxContianer">
            <div class="tabel-btns-container save-btn mb-4">
                <span class="greenBg"><router-link :to="'/AdmissionPhaseForm'"> اضافه مرحله تقديم </router-link></span>
            </div>
            <div class="table-style">
                <table class="grey-style">
                    <tr>
                    <th>
                        <input
                        type="checkbox"
                        />
                    </th>
                    <th>الاسم</th>
                    <th>النوع</th>
                    <th>تاريخ البدء</th>
                    <th>تاريخ الانتهاء</th>
                    <th>الحدث</th>
                    </tr>
                    <tr v-for="admission in admissionPhaseData" :key="admission.id">
                        <td>
                            <input type="checkbox"/>
                        </td>
                        <td>{{admission.title_ar}}</td>
                        <td><span v-for="gender in admission.gender" :key="gender.id"> {{gender.name_ar}} {{  gender.name_ar.length > 1 ?  ' /' : ''}}</span></td>
                        <td>{{admission.start_time}}</td>
                        <td>{{admission.end_time}}</td>
                        <td>
                            <span class="blueBg">تعديل</span>
                            <span class="redBg">حذف</span>    
                        </td>
                    </tr>
                    <tr>
                    <td colspan="8" v-if="emptyData()">
                        <h4 class="text-center">لا توجد مراحل مسجله</h4>
                    </td>
                    </tr>
                </table>

                <div class="tabel-btns-container save-btn">
                    <span class="redBg">حذف</span>
                    <span class="yellowBg">تعليق</span>
                    <span class="blackBg">ايقاف</span>
                </div>
            </div>
      </div>
    </div>
</template>
<script>
import axios from 'axios'
import store from '../../../store'
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
    name: "newAdmissionPhase",
    data(){
        return {
            admissionPhaseData : []
        }
    },
    mounted(){
        axios
        .get(store.state.domain + "/admissionPhaseInterval/listAll" , config)
        .then(res => this.admissionPhaseData = res.data.data)
    },
    methods: {
        emptyData(){
            this.admissionPhaseData.length < 1
        }
    }
    
}
</script>
<style lang="scss" scoped>
    td span{
        color: #000 !important;
        font-weight: 500;
        font-size: 16px !important;
    }
</style>