<template>
  <div class="main-content-admin">
      <div class="boxContianer">
        <b-row class="mb-2">
            <b-col>
                <span>اضافه تصنيف جديد +</span>
            </b-col>
        </b-row>
        <b-row>
            <b-col lg="5" class="data-item" offset-lg="1">
                <label>اسم التصنيف باللغة العربية</label>
                <b-form-input type="text" v-model="serviceCatAr"></b-form-input>
            </b-col>
            <b-col lg="5" class="data-item">
                <label>اسم التصنيف باللغة الانجليزية</label>
                <b-form-input type="text" v-model="serviceCatEn"></b-form-input>
            </b-col>
        </b-row>
        <div class="save-btn">
            <span class="blueBg" @click="OnSubmitCat">حفظ</span>
            <span class="redBg">الغاء</span>
        </div>
    </div>    
    <div class="boxContianer mt-5">
        <b-row>
            <b-col>
                <span>اضافه خدمه جديد +</span>
            </b-col>
        </b-row>
        <b-row>
            <b-col xl="6">
                <label>اسم التصنيف</label>
                <b-form-select disabled-field="notEnabled" v-model="servicesCategory">
                    <b-form-select-option :value="null" disabled>اختار تصنيف الخدمة</b-form-select-option>
                    <b-form-select-option v-for="(category,i) in servicesCategories" :key="i" :value="category.id">
                        {{category.name}}
                    </b-form-select-option>
                </b-form-select>
                </b-col>
        </b-row>
        <b-row class="mt-5">
            <b-col xl="6">
                <label>اسم الخدمه</label>
                    <b-form-input
                        type="text"
                    ></b-form-input>
            </b-col>
        </b-row>
        <b-row class="mt-5">
                <b-col xl="6">
                    <label>نوع الخدمه</label>
                    <b-form-select
                    disabled-field="notEnabled"
                    >
                    <b-form-select-option :value="null" disabled>اساسي/اضافي</b-form-select-option>
                    <b-form-select-option>
                    -
                    </b-form-select-option>
                    </b-form-select>
                </b-col>
        </b-row>
        <div class="save-btn">
            <button
            type="submit"
            class="blueBg"
            >
            حفظ
            </button>
            <span class="redBg">
                اللغاء
            </span>
        </div>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
    name: "ServicesCategory",
    data() {
        return {
            servicesCategories: "",
            serviceCatAr: "",
            serviceCatEn: "",
            servicesCategory: null,
            serviceTypes: ""

        }
    },
    mounted() {
        axios
            .get(store.state.domain + "/paymentFee", config)
            .then(res => (this.servicesCategories = res.data.data));
        axios
            .get(store.state.domain + "/serviceTypes", config)
            .then(res => (this.serviceTypes = res.data.data));
    },
    methods: {
        OnSubmitCat() {
        axios({
            method: "POST",
            url: store.state.domain + "/paymentFee",
            headers: {
                localization: store.state.localization
            },
            data: {
                translations: [
                    {
                        name: this.serviceCatAr,
                        description: this.serviceCatAr,
                        locale : "ar"
                    },
                    {
                        name: this.serviceCatEn,
                        description: this.serviceCatEn,
                        locale : "en"
                    }
                ]
            }
        })
            .then(res => {
            if (res.status == 200 || res.status == 201) {
                this.$router.push("/services");
            }
            })
            .catch(error => {
            this.err = error.response.data.errors;
            });
        },
    }
}
</script>