<template>
  <div class="main-content-admin">
    <h2 class="text-center">جدول الوجبات</h2>
    <p v-if="this.timeTables.length == 0">لا يوجد جداول</p>
    <b-row v-for="(timeTable, index) in timeTables" :key="index">
      <b-col>
        <b-button class="coll-ing-btn" v-b-toggle="'collapse-' + index">
          <font-awesome-icon icon="sort-down" />
          <span>{{ timeTable.name_ar }}</span>
        </b-button>
        <b-collapse visible :id="'collapse-' + index">
          <div class="btns-header mt-4">
            <b-button class="ml-3">
              Active/De-active
            </b-button>
            <div class="save-btn">
              <button type="submit" class="greenBg">
                تعديل
              </button>
              <span class="redBg" @click="deleteData(timeTable.id)">حذف</span>
            </div>
          </div>
          <div class="boxContianer">
            <div class="table-style">
              <div class="divTable">
                <div class="table-header">
                  <div class="table-th">اليوم</div>
                  <div
                    class="table-th"
                    v-for="(timeTableInfo,
                    index) in timeTable.timetable_information"
                    :key="index"
                  >
                    {{ timeTableInfo.day }}
                  </div>
                </div>
                <div
                  class="table-header"
                  v-for="(mealtype, index) in mealtypes"
                  :key="index"
                >
                  <div class="table-th">{{ mealtype.name_ar }}</div>
                  <div
                    class="table-th"
                    v-for="(timeTableInfo,
                    index) in timeTable.timetable_information"
                    :key="index"
                  >
                    <slot v-for="meal in meals">
                      <slot v-for="time in timeTableInfo.meal_schedule">
                        <span
                          v-if="
                            mealtype.id == time.category_id &&
                              meal.id == time.meal_id
                          "
                          :key="meal.id + time.id"
                          >{{ meal.name_ar }}</span
                        >
                      </slot>
                    </slot>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </b-collapse>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="save-btn">
        <span class="blueBg"
          ><router-link :to="'/feeding/createTimeTable'"
            >اضافه جدول جديد
          </router-link></span
        >
      </b-col>
    </b-row>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
  name: "TimeTable",
  data() {
    return {
      timeTables: [],
      meals: [],
      mealtypes: []
    };
  },
  mounted() {
    axios
      .get(store.state.domain + "/mealTime", config)
      .then(res => (this.timeTables = res.data.data));
    axios
      .get(store.state.domain + "/meal", config)
      .then(res => (this.meals = res.data.data));
    axios
      .get(store.state.domain + "/mealtype", config)
      .then(res => (this.mealtypes = res.data.data));
  },
  methods: {
    deleteData(id) {
      const $this = this;
      axios
        .delete(store.state.domain + "/mealTime/" + id, { id: id }, config)
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.save-btn {
  margin-top: 0;
}
.btns-header {
  display: flex;
  align-items: center;
}
.divTable {
  display: flex;
  width: 100%;
  color: rgba(0, 0, 0, 0.822);
  .table-header {
    display: grid;
    width: 100%;
    background-color: #f9f9f9;
    .table-th {
      display: table-cell;
      border: 1px solid rgba(0, 0, 0, 0.315);
      padding: 11px 10px;
      font-size: 17px;
      height: 55px;
    }
    .table-th:first-child {
      color: #008eb9;
      font-weight: 600;
    }
  }
}
</style>
