<template>
  <div class="main-content-admin">
    <vue-html2pdf
      :show-layout="controlValue.showLayout"
      :float-layout="controlValue.floatLayout"
      :enable-download="controlValue.enableDownload"
      :preview-modal="controlValue.previewModal"
      :filename="controlValue.filename"
      :paginate-elements-by-height="controlValue.paginateElementsByHeight"
      :pdf-quality="controlValue.pdfQuality"
      :pdf-format="controlValue.pdfFormat"
      :pdf-orientation="controlValue.pdfOrientation"
      :pdf-content-width="controlValue.pdfContentWidth"
      :manual-pagination="controlValue.manualPagination"
      :html-to-pdf-options="htmlToPdfOptions"
      @progress="onProgress($event)"
      @startPagination="startPagination()"
      @hasPaginated="hasPaginated()"
      @beforeDownload="beforeDownload($event)"
      @hasDownloaded="hasDownloaded($event)"
      ref="html2Pdf"
    >
      <pdf-content
        @domRendered="domRendered()"
        slot="pdf-content"
        :charts="this.reports.selectedCharts"
      />
    </vue-html2pdf>
    <div>
      <b-row>
        <b-col xl="6">
          <label for="">اسم التقرير</label>
          <b-form-input v-model="pdfTitle" type="text"></b-form-input>
        </b-col>
        <b-col xl="6">
          <label for="">اختار لغه التقرير</label>
          <b-form-select disabled-field="notEnabled" v-model="reports.lang">
            <b-form-select-option :value="null" disabled
              >اختار لغه التقرير</b-form-select-option
            >
            <b-form-select-option></b-form-select-option>
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="6">
          <label for="">اختار الشعار</label>
          <b-form-select v-model="reports.selectLogo" :options="logoOptions"></b-form-select>
          <b-form-select-option :value="null" disabled>اختار الشعار</b-form-select-option>
        </b-col>
        <b-col xl="6">
          <label for="">صنف ب</label>
          <b-form-select v-model="reports.filterBy" disabled-field="notEnabled">
            <b-form-select-option :value="null" disabled
              >صنف ب</b-form-select-option
            >
            <b-form-select-option></b-form-select-option>
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="12" class="mt-3">
          <h4>فترة التقرير</h4>
        </b-col>
        <b-col xl="6">
          <label>من يوم</label>
          <b-form-datepicker
            v-model="startDate"
            locale="ar"
            :min="currentDate"
            :initial-date="currentDate"
            v-bind="labels || {}"
            required
            :state="StartingDateState"
          ></b-form-datepicker>
        </b-col>
        <b-col xl="6">
          <label for="">الى</label>
          <b-form-datepicker
            v-model="endDate"
            locale="ar"
            :min="startDate"
            :max="currentDate"
            v-bind="labels || {}"
            required
            :state="EndingDateState"
          ></b-form-datepicker>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="6">
          <label for="">اختار الرسم البياني</label>
          <b-dropdown text="اختار الرسم البياني" ref="dropdown">
            <b-form-checkbox-group v-model="reports.selectedCharts">
              <b-form-checkbox class="mb-3" :value="1"
                >عدد طلبة المدن من المحافظات المختلفة</b-form-checkbox
              >
              <b-form-checkbox class="mb-3" :value="2"
                >عدد طلبة المدن من الكليات المختلفة</b-form-checkbox
              >
              <b-form-checkbox class="mb-3" :value="3"
                >الطلبة الأجانب والمصريين بالمدينة الجامعية</b-form-checkbox
              >
            </b-form-checkbox-group>
          </b-dropdown>
        </b-col>
        <b-col xl="6">
          <label for="">تكرار كل</label>
          <b-form-input
            v-model="reports.repeatedEvery"
            type="text"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="12" class="mt-3">
          <h4>الإمضاءات</h4>
        </b-col>
        <b-col xl="6" v-for="(employee, index) in employeesPDF" :key="index">
          <h5 v-if="employee.name">امضاء {{ employee.name }}</h5>
          <label for="">اسم الوظيفه</label>
          <b-form-input v-model="employee.job" type="text"></b-form-input>
          <label for="">اسم الموظف</label>
          <b-form-input v-model="employee.name" type="text"></b-form-input>
        </b-col>
        <b-col xl="12" class="mt-3 save-btn">
          <span class="blueBg" @click="addNew">اضافة امضاء</span>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="12" class="mt-3">
          <h4>شهادة وحدة الجودة</h4>
        </b-col>
        <b-col xl="6">
          <label for="">رقم النموذج</label>
          <b-form-input v-model="reports.modelId" type="text"></b-form-input>
        </b-col>
        <b-col xl="6">
          <label for="">الاصدار</label>
          <b-form-input v-model="reports.version" type="text"></b-form-input>
        </b-col>
      </b-row>
      <div class="save-btn">
        <button type="submit" class="blueBg" @click="downloadPdf()">حفظ</button>
        <span class="greenBg">عرض</span>
        <span class="redBg">الغاء</span>
      </div>
    </div>

    <controls-container :progress="progress" />
  </div>
</template>

<script>
import PdfContent from "./PdfContent";
import ControlsContainer from "./ControlsContainer";
import VueHtml2pdf from "vue-html2pdf";
import { mapFields } from "vuex-map-fields";

export default {
  name: "PDFReport",
  data() {
    return {
      contentRendered: false,
      logoOptions: [
          { value: null, text: 'اختار الشعار' },
          { value: '@/assets/Iskan-square.png', text: 'المدن الجامعية' },
          { value: '', text: 'الجودة' },
      ],
      progress: 0,
      generatingPdf: false,
      pdfDownloaded: false,
      currentDate: new Date().toISOString().substring(0, 10),
      labels: {
        labelNoDateSelected: "لم يتم اختيار التاريخ",
        labelNoTimeSelected: "لم يتم اختيار الوقت",
        labelCloseButton: "اختار"
      },
      reports: {
        logo: "",
        lang: null,
        selectLogo: null,
        filterBy: null,
        selectedCharts: [],
        repeatedEvery: "",
        selectColumns: "",
        jobTitle: "",
        employeeName: "",
        order: "",
        modelId: "",
        version: ""
      }
    };
  },

  computed: {
    ...mapFields(["controlValue"]),
    pdfTitle: {
      get() {
        return this.$store.state.pdfReportData.title;
      },
      set(value) {
        this.$store.commit("setPDFtitle", value);
      }
    },
    startDate: {
      get() {
        return this.$store.state.pdfReportData.startDate;
      },
      set(value) {
        this.$store.commit("setReportStartDate", value);
      }
    },
    endDate: {
      get() {
        return this.$store.state.pdfReportData.endDate;
      },
      set(value) {
        this.$store.commit("setReportEndDate", value);
      }
    },
    employeesPDF: {
      get() {
        return this.$store.state.pdfReportData.employees;
      },
      set(value) {
        this.$store.commit("setPDFemployees", value);
      }
    },
    StartingDateState() {
      if (this.$store.state.pdfReportData.startDate.length == 0) {
        return null;
      } else if (this.$store.state.pdfReportData.startDate.length > 9) {
        return true;
      } else {
        return false;
      }
    },
    EndingDateState() {
      if (this.$store.state.pdfReportData.endDate.length == 0) {
        return null;
      } else if (this.$store.state.pdfReportData.endDate.length > 9) {
        return true;
      } else {
        return false;
      }
    },
    htmlToPdfOptions() {
      return {
        margin: 0,
        filename: "hee hee.pdf",
        image: {
          type: "jpeg",
          quality: 0.98
        },
        enableLinks: true,
        html2canvas: {
          scale: this.controlValue.pdfQuality,
          useCORS: true
        },
        jsPDF: {
          unit: "in",
          format: this.controlValue.pdfFormat,
          orientation: this.controlValue.pdfOrientation
        }
      };
    }
  },

  methods: {
    async downloadPdf() {
      if (!(await this.validateControlValue())) return;

      this.$refs.html2Pdf.generatePdf();
    },
    addNew() {
      this.$store.state.pdfReportData.employees.push({
        name: "",
        job: "",
        order: ""
      });
    },
    validateControlValue() {
      if (this.controlValue.pdfQuality > 2) {
        alert("pdf-quality value should only be 0 - 2");
        this.controlValue.pdfQuality = 2;

        return false;
      }

      if (!this.controlValue.paginateElementsByHeight) {
        alert("paginate-elements-by-height value cannot be empty");
        this.controlValue.paginateElementsByHeight = 1400;

        return false;
      }

      const paperSizes = [
        "a0",
        "a1",
        "a2",
        "a3",
        "a4",
        "letter",
        "legal",
        "a5",
        "a6",
        "a7",
        "a8",
        "a9",
        "a10"
      ];

      if (!paperSizes.includes(this.controlValue.pdfFormat)) {
        alert(`pdf-format value should only be ${paperSizes}`);
        this.controlValue.pdfFormat = "a4";

        return false;
      }

      if (!this.controlValue.pdfOrientation) {
        alert("pdf-orientation value cannot be empty");
        this.controlValue.pdfOrientation = "portrait";

        return false;
      }

      if (!this.controlValue.pdfContentWidth) {
        alert("pdf-content-width value cannot be empty");
        this.controlValue.pdfContentWidth = "800px";

        return false;
      }

      return true;
    },

    onProgress(progress) {
      this.progress = progress;
      console.log(`PDF generation progress: ${progress}%`);
    },

    startPagination() {
      console.log(`PDF has started pagination`);
    },

    hasPaginated() {
      console.log(`PDF has been paginated`);
    },

    hasDownloaded(blobPdf) {
      console.log(`PDF has downloaded yehey`);
      this.pdfDownloaded = true;
      console.log(blobPdf);
    },

    domRendered() {
      console.log("Dom Has Rendered");
      this.contentRendered = true;
    },

    onBlobGenerate(blob) {
      console.log(blob);
    }
  },

  components: {
    VueHtml2pdf,
    PdfContent,
    ControlsContainer
  }
};
</script>

<style lang="scss" scoped>
.dropdown {
  width: 100%;
  ul .bv-no-focus-ring {
    text-align: right;
  }
}
</style>
