<template>
  <div class="main-content-admin">
    <Dormstabs tab="rooms" />
    <form class="label-size">
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1" @change="PartitionBuildingInfo">
          <label>اختار المدينة</label>
          <b-form-select
            :class="partitionInfo.dorm != null ? 'is-valid' : ''"
            v-model="partitionInfo.dorm"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار المدينة</b-form-select-option
            >
            <b-form-select-option
              v-for="dorm in dorms"
              :key="dorm.id"
              :value="dorm.id"
              >{{ dorm.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5" class="data-item">
          <label>اختار المجموعة</label>
          <b-form-select
            :class="partitionInfo.group != null ? 'is-valid' : ''"
            disabled-field="notEnabled"
            v-model="partitionInfo.group"
          >
            <b-form-select-option :value="null" disabled
              >اختار المجموعة</b-form-select-option
            >
            <b-form-select-option
              v-for="group in groups"
              :key="group.id"
              :value="group.id"
              >{{ group.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>اختار المبنى</label>
          <b-form-select
            :class="partitionInfo.building != null ? 'is-valid' : ''"
            v-model="partitionInfo.building"
            disabled-field="notEnabled"
            @change="PartitionFloorInfo"
          >
            <b-form-select-option :value="null" disabled
              >اختار المبنى</b-form-select-option
            >
            <b-form-select-option
              v-for="building in buildings"
              :key="building.id"
              :value="building.id"
              >{{ building.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col cols="5" class="data-item">
          <label>اختار الدور</label>
          <b-form-select
            :class="partitionInfo.floorNo != null ? 'is-valid' : ''"
            v-model="partitionInfo.floorNo"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار الدور</b-form-select-option
            >
            <b-form-select-option
              v-for="floor in floors"
              :key="floor.id"
              :value="floor.id"
              >{{ floor.floor_number }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>اسم الغرفة</label>
          <b-form-input
            type="text"
            v-model="partitionInfo.room"
            :state="roomState"
            required
          ></b-form-input>
        </b-col>
        <b-col lg="5" class="data-item">
          <label>سعة الغرفة</label>
          <b-form-input
            type="text"
            v-model="partitionInfo.capacity"
            :state="capacityState"
            required
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>نوع الغرفة</label>
          <b-form-select
            :class="partitionInfo.partitionType != null ? 'is-valid' : ''"
            v-model="partitionInfo.partitionType"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >نوع الغرفة</b-form-select-option
            >
            <b-form-select-option
              v-for="partition in partitionType"
              :key="partition.id"
              :value="partition.id"
              >{{ partition.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5">
          <label>الحالة</label>
          <b-form-select
            v-model="partitionInfo.status"
            :options="options"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار الحالة</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>الكلية</label>
          <b-form-select
            :class="partitionInfo.faculty != null ? 'is-valid' : ''"
            v-model="partitionInfo.faculty"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >الكلية</b-form-select-option
            >
            <b-form-select-option
              v-for="faculty in faculties"
              :key="faculty.id"
              :value="faculty.id"
              >{{ faculty.faculty_name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5">
          <label>الجنسية</label>
          <b-form-select
            :class="partitionInfo.nationality != null ? 'is-valid' : ''"
            v-model="partitionInfo.nationality"
            :options="nationality"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار الجنسية</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>      
        <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>محتويات الغرفة</label>
          <b-dropdown text="محتويات الغرفة" ref="dropdown">
              <b-form-checkbox v-model="selected" :value="asset" v-for="(asset, i) in assetsData" :key="i">{{asset.name_ar}}</b-form-checkbox>
          </b-dropdown>
        </b-col>
      </b-row>
      <b-row>
        <div class="show-data table-style" v-if="selected.length > 0">
          <table class="grey-style">
            <tr>
              <th>الأصول</th>
              <th>الكمية</th>
              <th>حذف</th>
            </tr>
            <tr v-for="(select, index) in selected" :key="index">
              <td>{{ selected[index].name_ar }}</td>
              <td>
                *
                <b-form-input
                  type="number"
                  required
                  v-model.number="selected[index].quantity"
                ></b-form-input>
              </td>
              <td><span class="redBg" @click="deleteitem(index)">حذف</span></td>
            </tr>
          </table>
        </div>
      </b-row>
      <b-row v-if="err.length != 0">
        <ul>
          <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
          <li v-if="this.err.floor_id == 'The floor id field is required.'">
            أدخل رقم الدور
          </li>
          <li
            v-if="
              this.err.partition_number ==
                'The partition number has already been taken.'
            "
          >
            رقم الغرفة موجود مسبقا
          </li>
          <li v-if="this.err.floor_id == 'The selected floor id is invalid.'">
            رقم الدور غير صحيح
          </li>
          <li v-if="this.err.status == 'The status field is required.'">
            أدخل الحالة
          </li>
          <li
            v-if="
              this.err.partition_type_id ==
                'The partition type id field is required.'
            "
          >
            اختار نوع الغرفة
          </li>
          <li
            v-if="
              this.err.partition_capacity ==
                'The partition capacity field is required.'
            "
          >
            حدد سعة الغرفة
          </li>
          <li
            v-if="
                this.err.partition_number ==
                  'The partition number must be an integer.'
            "
          >
            رقم الغرفة مكون من أرقام فقط ولا يقبل أحرف أو رموز
          </li>
          <li
            v-if="
                this.err.partition_capacity ==
                  'The partition capacity must be an integer.'
            "
          >
            سعة الغرفة مكون من أرقام فقط ولا يقبل أحرف أو رموز
          </li>
        </ul>
      </b-row>
      <div class="save-btn">
        <button
          type="submit"
          class="blueBg"
          @click.prevent="OnSubmit"
          v-if="recordForUpdate == null"
        >
          حفظ
        </button>
        <button
          type="submit"
          class="blueBg"
          @click.prevent="UpdatePartition"
          v-if="recordForUpdate != null"
        >
          تعديل
        </button>
        <span class="redBg" @click="resetData">إلغاء</span>
      </div>
    </form>
    <div class="show-data table-style">
      <table class="grey-style">
        <tr>
          <th>
            <input
              type="checkbox"
              @click="select_all_via_check_box"
              v-model="all_select"
            />
          </th>
          <th>الغرفة</th>
          <th>الحالة</th>
          <th>تعديل</th>
        </tr>
        <tr v-for="partition in partitions" :key="partition.id">
          <td>
            <input
              type="checkbox"
              v-model="updateItems"
              :value="partition.id"
            />
          </td>
          <td>{{ partition.partition_number }}</td>
          <td v-if="partition.status == 'Active'" class="greenFont">
            مفعل
          </td>
          <td v-if="partition.status == 'Off'" class="redFont">
            مغلق
          </td>
          <td>
            <span class="blueBg" @click.prevent="returnPartition(partition.id)"
              >تعديل</span
            >
          </td>
        </tr>
        <tr v-if="emptyData()">
          <td colspan="5"><h4 class="text-center">لا توجد غرف مسجلة</h4></td>
        </tr>
      </table>

      <div class="tabel-btns-container save-btn" v-if="fullData()">
        <span class="redBg" @click="disableStatus">تعليق</span>
        <span class="greenBg" @click="activeStatus">تفعيل</span>
        <span class="redBg" @click="deleteData">حذف</span>
      </div>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
import Dormstabs from "./DormsTabs";
const config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "createPartition",
  components: {
    Dormstabs
  },
  data() {
    return {
      recordForUpdate: null,
      selected: [],
      err: [],
      all_select: false,
      updateItems: [],
      dorms: "",
      genders: "",
      currentDorm: "",
      groups: "",
      buildings: "",
      floors: "",
      faculties: "",
      assetsData: [],
      assets: "",
      partitions: [],
      partitionInfo: {
        dorm: null,
        group: null,
        building: null,
        faculty: null,
        status: null,
        floorNo: null,
        room: "",
        capacity: "",
        nationality: null,
        partitionType: null
      },
      nationality: [
        { item: "active", name: "مصري" },
        { item: "off", name: "أجنبي" }
      ],
      options: [
        { item: "active", name: "مفعل" },
        { item: "off", name: "مغلق" }
      ],
      partitionType: ""
    };
  },
  mounted() {
    this.getTableResults();
    axios
      .get(store.state.domain + "/dorm", config)
      .then(res => (this.dorms = res.data.data));
    axios
      .get(store.state.domain + "/buildingGroup", config)
      .then(res => (this.groups = res.data.data));
    axios
      .get(store.state.domain + "/facultiesSelection/listAllFaculties", config)
      .then(res => (this.faculties = res.data.data));
    axios
      .get(store.state.domain + "/partitionType", config)
      .then(res => (this.partitionType = res.data.data));
    axios
      .get(store.state.domain + "/assets", config)
      .then(res => (this.assetsData = res.data.data));
  },

  computed: {
    roomState() {
      if (this.partitionInfo.room.length == 0) {
        return null; 
      } else {
        return true;
      }
    },
    capacityState() {
      if (this.partitionInfo.capacity.length == 0) {
        return null; 
      } else {
        return true;
      }
    },
  },
  methods: {
    getTableResults() {
      axios
        .get(store.state.domain + "/floorPartition", config)
        .then(res => (this.partitions = res.data.data)).catch(error => {
          this.err = error.response.data.errors;
        });
    },
    PartitionBuildingInfo() {
      if (this.partitionInfo.dorm == null) {
        return this.partitionInfo.building == null
      } else if (this.partitionInfo.dorm > 0 ) {
     axios
      .get(store.state.domain + "/dormBuilding?dorm_id=" + this.partitionInfo.dorm, config)
      .then(res => (this.buildings = res.data.data)).catch(error => {
          this.err = error.response.data.errors;
        });
      }
    },
    PartitionFloorInfo() {
      if (this.partitionInfo.building == null) {
        return this.partitionInfo.floor == null
      } else if (this.partitionInfo.building > 0 ) {
     axios
      .get(store.state.domain + "/buildingFloor?building_id=" + this.partitionInfo.building, config)
      .then(res => (this.floors = res.data.data)).catch(error => {
          this.err = error.response.data.errors;
        });
      }
    },
    OnSubmit() {
      axios({
        method: "POST",
        url: store.state.domain + "/floorPartition",
        headers: {
          localization: store.state.localization
        },
        data: {
          floor_id: this.partitionInfo.floorNo,
          status: this.partitionInfo.status,
          partition_number: this.partitionInfo.room,
          partition_type_id: this.partitionInfo.partitionType,
          partition_capacity: this.partitionInfo.capacity,
          assets: this.selected
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.getTableResults();
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    resetData() {
      (this.partitionInfo.building = null),
        (this.partitionInfo.floorNo = null),
        (this.partitionInfo.status = null),
        (this.partitionInfo.dorm = null),
        (this.partitionInfo.group = null),
        (this.partitionInfo.faculty = null),
        (this.partitionInfo.nationality = null),
        (this.partitionInfo.room = ""),
        (this.partitionInfo.capacity = ""),
        (this.recordForUpdate = null),
        (this.selected = [])
    },
    returnPartition(id) {
      this.recordForUpdate = id;
      axios.get(store.state.domain + "/floorPartition/" + id, config).then(res => {
        (this.partitionInfo.floorNo = res.data.data.floor_id),
          (this.partitionInfo.status = res.data.data.status),
          (this.partitionInfo.room = res.data.data.partition_number),
          (this.partitionInfo.capacity = res.data.data.capacity);
      }).catch(error => {
          this.err = error.response.data.errors;
      });
    },
    activeStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/floorPartition/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "active"
          }
        }).then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    disableStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/floorPartition/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "off"
          }
        }).then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    UpdatePartition() {
      axios({
        method: "PUT",
        url: store.state.domain + "/floorPartition/" + this.recordForUpdate,
        headers: {
          localization: store.state.localization
        },
        data: {
          floor_id: this.partitionInfo.floorNo,
          status: this.partitionInfo.status,
          partition_number: this.partitionInfo.room,
          partition_type_id: this.partitionInfo.partitionType,
          partition_capacity: this.partitionInfo.capacity
        }
      }).then(res => {
        if (res.status == 200 || res.status == 201) {
          this.getTableResults();
        }
      }).catch(error => {
          this.err = error.response.data.errors;
      });
    },
    deleteData() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios
          .delete(store.state.domain + "/floorPartition/" + id, {
            id: id
          }, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.getTableResults();
            }
          }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    select_all_via_check_box() {
      if (this.all_select == false) {
        this.all_select = true;
        this.partitions.forEach(partition => {
          this.updateItems.push(partition.id);
        });
      } else {
        this.all_select = false;
        this.updateItems = [];
      }
    },
    deleteitem(index) {
      let indexBefore = this.selected.indexOf(index);
      this.selected.splice(indexBefore, 1);
    },
    emptyData() {return this.partitions.length < 1;},
    fullData() {return this.partitions.length >= 1;}
  }
};
</script>
<style lang="scss" scoped>
  .dropdown, .dropdown-menu.show {
    width: 100%;
    label {
      text-align: right
    }
  }
</style>
 