<template>
  <div class="category-type">
    <div class="main-content-admin">
      <div class="add-new-subject">
        <div class="add-container">
            <AddCategory addCategoryApi="/mealtype" :reloadMealCategoryResults="reloadMealCategoryResults"/>
        </div>
        <div class="add-container">
            <SubCategory
              addSubCategoryApi="/mealSubcategory"
              addCategoryApi="/mealtype"
              :reloadMealSubcategoryResults="reloadMealSubcategoryResults"
              :reloadMealCategoryResults="reloadMealCategoryResults"
            />
        </div>
        <div class="add-container">
          <router-link :to="{ name: 'AddMeal' }">
            <span>اضافة وجبه جديده</span>
          </router-link>
        </div>
      </div>
      <div class="category-type" v-for="(meal, index) in meals" :key="index">
        <b-button class="coll-ing-btn" v-b-toggle="'collapse-'+index"
          >{{ meal.name_ar }}
          <font-awesome-icon icon="sort-down" />
        </b-button>
        <b-icon-trash @click="deleteMainCategory(meal.id)"></b-icon-trash>
        <b-collapse visible :id="'collapse-'+index">
          <b-card>
            <ul class="categories" v-if="mealsSubCategories.length > 0">
              <li
                v-for="(mealsSubCategory, index) in mealsSubCategories"
                :key="index"
              >
                <b-button
                  class="item-btn coll-ing-btn"
                  v-b-toggle="'collapse-inner-'+index"
                  size="sm"
                  v-if="meal.id == mealsSubCategory.meal_type_id.id"
                  >{{ mealsSubCategory.name_ar
                  }}<font-awesome-icon icon="sort-down"/>
                <b-icon-trash @click="deleteSubCategory(mealsSubCategory.id)"></b-icon-trash>
                </b-button>
                <b-collapse :id="'collapse-inner-'+index" class="mt-2">
                  <ul>
                    <slot v-for="(mealPart, index) in mealParts">
                    <li :key="index" v-if="meal.id == mealsSubCategory.meal_type_id.id && mealsSubCategory.id  == mealPart.meal_subcategory_id.id">
                      <input type="checkbox">
                      <span>{{mealPart.name_ar}}</span>
                    </li>
                    </slot>
                  </ul>
                </b-collapse>
              </li>
            </ul>
            <ul class="categories" v-else>
              <p>لا توجد مكونات</p>
                  </ul>
          </b-card>
        </b-collapse>
      </div>
      <div class="save-btn">
        <span class="blueBg"><router-link :to="'/feeding/createRequest'">انشاء طلب</router-link></span>
        <span class="redBg">حذف</span>
      </div>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
import AddCategory from "../Ingredients/AddCategory";
import SubCategory from "../Ingredients/SubCategory";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faEdit, faSortDown } from "@fortawesome/free-solid-svg-icons";
library.add(faEdit, faSortDown);
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "Meals",
  components: {
    AddCategory,
    SubCategory
  },
  data() {
    return {
      meals: [],
      mealsSubCategories: [],
      mealParts: []
    };
  },
  mounted() {
    this.reloadMealCategoryResults();
    this.reloadMealSubcategoryResults();
    axios
      .get(store.state.domain + "/meal", config)
      .then(res => (this.mealParts = res.data.data));
  },
  methods: {
    reloadMealCategoryResults(){
      axios
      .get(store.state.domain + "/mealtype", config)
      .then(res => (this.meals = res.data.data));
    },
    reloadMealSubcategoryResults(){
      axios
      .get(store.state.domain + "/mealSubcategory", config)
      .then(res => (this.mealsSubCategories = res.data.data));
    },
    deleteMainCategory(id) {
      const $this = this;
        axios
          .delete(store.state.domain + "/mealtype/" + id, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.reloadMealCategoryResults();
            }
        });
    },
    deleteSubCategory(id) {
      const $this = this;
        axios
          .delete(store.state.domain + "/mealSubcategory/" + id, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.reloadMealSubcategoryResults();
            }
        });
    },
  }
};
</script>
