<template>
  <div class="main-content-admin">
    <h4 class="text-center">استمارة تطبيق مخالفة على الطالب</h4>
    <form>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>اسم الطالب</label>
          <b-form-input type="text" required></b-form-input>
          <b-form-invalid-feedback>
            ادخل اسم الطالب
          </b-form-invalid-feedback>
        </b-col>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>كود الطالب</label>
          <b-form-input type="text" required></b-form-input>
          <b-form-invalid-feedback>
            أدخل كود الطالب
          </b-form-invalid-feedback>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>نوع المخالفة</label>
          <b-form-select v-model="infraction.type" disabled-field="notEnabled">
              <b-form-select-option :value="null" disabled
                >اختار المخالفة</b-form-select-option
              >
              <b-form-select-option
                v-for="infraction in infractions"
                :key="infraction.id"
                :value="infraction.id"
                >{{ infraction.name_ar }}</b-form-select-option
              >
            </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>تاريخ تسجيل المخالفة</label>
          <b-form-datepicker
            v-model="infraction.date"
            locale="ar"
            :min="currentDate"
            :initial-date="currentDate"
            v-bind="labels || {}"
            required
          ></b-form-datepicker>
        </b-col>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>توقيت تسجيل المخالفة</label>
          <b-form-timepicker
            locale="ar"
            v-model="infraction.time"
            v-bind="labels || {}"
            required
          ></b-form-timepicker>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="11" class="data-item" offset-lg="1">
          <label>ملاحظات</label>
          <b-form-textarea rows="3" v-model="infraction.notes"></b-form-textarea>
        </b-col>
      </b-row>
      <div class="save-btn">
        <button type="submit" class="blueBg" @click.prevent="OnSubmit">
          حفظ
        </button>
        <span class="redBg">إلغاء</span>
      </div>
    </form>
  </div>
</template>

<script>
import store from "@/store/index.js";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
  name: "createInfraction",
  data() {
    return {
      err: [],
      currentDate: new Date().toISOString().substring(0, 10),
      labels: {
        labelNoDateSelected: "لم يتم اختيار التاريخ",
        labelNoTimeSelected: "لم يتم اختيار الوقت",
        labelMinutes: "الدقائق",
        labelHours: "ساعات",
        labelSeconds: "ثواني",
        labelAmpm: "صباحا مساء",
        labelAm: "ص",
        labelPm: "م",
        labelCloseButton: "اختار"
      },
      infractions: [],
      penalities: [],
      infraction: {
        type: null,
        date: "",
        time: "", 
        notes: ""
      }
    };
  },
  mounted() {
    axios
      .get(store.state.domain + "/infractions", config)
      .then(res => (this.infractions = res.data.data));
  },
  methods: {
    OnSubmit: function() {
      axios({
        method: "POST",
        url: store.state.domain + "/studentInfractions/store",
        headers: {
          localization: store.state.localization
        },
        data: {
          student_id: 8,
          infraction_id: this.infraction.type,
          infraction_date: this.infraction.date,
          infraction_time: this.infraction.time,
          note: this.infraction.notes
        }
      });
    },
    resetData() {
      this.infraction.nameAr = "";
      this.infraction.nameEn = "";
      this.infraction.desAr = "";
      this.infraction.desEn = null;
    }
  }
};
</script>
