<template>
  <div class="main-content-admin">
    <Dormstabs tab="cities" />
    <form>
      <h2 class="text-center" v-if="!this.$route.params.id">إضافة مدينة</h2>
      <h2 class="text-center" v-if="this.$route.params.id">
        تعديل {{ dorm.dormNameAr }}
      </h2>
      <b-row>
        <b-col>
          <label>اسم المدينة باللغة العربية</label>
          <b-form-input
          type="text"
          v-model="dorm.dormNameAr"
          :state="cityArState"
          required
        ></b-form-input>
        <b-form-invalid-feedback>
            ادخل اسم المدينة باللغة العربية
        </b-form-invalid-feedback>
        </b-col>
        <b-col>
          <label>اسم المدينة باللغة الانجليزية</label>
          <b-form-input
          type="text"
          v-model="dorm.dormNameEn"
          :state="cityEnState"
          required
        ></b-form-input>
        <b-form-invalid-feedback>
            ادخل اسم المدينة باللغة الانجليزية
        </b-form-invalid-feedback>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <label>العنوان</label>
          <b-form-input
          type="text"
          v-model="dorm.dormAddress"
          :state="cityAddress"
          required
        ></b-form-input>
        </b-col>
        <b-col>
          <label>الحالة</label>
          <b-form-select
            :class="status != null ? 'is-valid' : ''"
            v-model="status"
            :options="options"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار الحالة</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row v-if="err.length != 0">
        <ul class="form--error">
          <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
          <li
            v-if="
              this.err.address_ar == 'The address ar field is required.'
            "
          >
            أدخل العنوان
          </li>
          <li
            v-if="
              this.err.name_ar == 'The name ar field is required.'
            "
          >
            أدخل اسم المدينة باللغة العربية
          </li>
          <li
            v-if="
              this.err.name_en == 'The name en field is required.'
            "
          >
            أدخل اسم المدينة باللغةالانجليزية
          </li>
          <li v-if="this.err.status == 'The status field is required.'">
            اختار حالة المبنى
          </li>
        </ul>
      </b-row>
      <div class="save-btn">
        <button
          type="submit"
          @click.prevent="UpdateDorm"
          class="blueBg"
          v-if="this.$route.params.id"
        >
          تعديل
        </button>
        <button
          type="submit"
          @click.prevent="OnSubmit"
          class="blueBg"
          v-if="!this.$route.params.id"
        >
          حفظ
        </button>
        <button
          type="submit"
          @click.prevent="OnNext"
          class="blueBg"
          v-if="!this.$route.params.id"
        >
          التالى
        </button>
        <span class="redBg" @click="resetData">الغاء</span>
      </div>
    </form>
  </div>
</template>

<script>
import store from "@/store/index.js";
import axios from "axios";
import Dormstabs from "./DormsTabs";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "createDorm",
  components: {
    Dormstabs
  },
  data() {
    return {
      err: [],
      status: null,
      dorms: "",
      dorm: {
        dormNameAr: "",
        dormNameEn: "",
        dormAddress: ""
      },
      options: [
        { item: "active", name: "مفعل" },
        { item: "off", name: "مغلق" }
      ]
    };
  },
  mounted() {
    if (this.$route.params.id) {
      axios
        .get(store.state.domain + "/dorm/" + this.$route.params.id, config)
        .then(res => {
          (this.dorm.dormNameAr = res.data.data.name_ar),
          (this.dorm.dormNameEn = res.data.data.name_en),
          (this.dorm.dormAddress = res.data.data.address_ar),
          (this.dorm.status = res.data.data.status.charAt(0).toLowerCase() + res.data.data.status.slice(1));
        });
    }
  },
  computed: {
    cityArState() {
      if (this.dorm.dormNameAr.length == 0) {
        return null; 
      } else if (!this.dorm.dormNameAr.match(/[\u0600-\u06FF\u0750-\u077F]/)) {
        return false
      } else if (this.dorm.dormNameAr.length > 3) {
        return true;
      } else {
        return false;
      }
    },
    cityEnState() {
      if (this.dorm.dormNameEn.length == 0) {
        return null; 
      } else if (!this.dorm.dormNameEn.match(/^[a-zA-Z\s]*$/)) {
        return false
      } else if (this.dorm.dormNameEn.length > 3) {
        return true;
      } else {
        return false;
      }
    },
    cityAddress() {
      if (this.dorm.dormAddress.length == 0) {
        return null; 
      } else if (this.dorm.dormAddress.length > 5) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    resetData() {
      this.dorm.dormNameAr = "";
      this.dorm.dormNameEn = "";
      this.dorm.dormAddress = "";
      this.status = null;
    },
    OnSubmit: function() {
      axios({
        method: "POST",
        url: store.state.domain + "/dorm",
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.dorm.dormNameAr,
          name_en: this.dorm.dormNameEn,
          status: this.status,
          address_ar: this.dorm.dormAddress
        }
      }).then(this.$router.push("/dorms")).catch(error => {
        this.err = error.response.data.errors;
      });
    }, 
    OnNext: function() {
      axios({
        method: "POST",
        url: store.state.domain + "/dorm",
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.dorm.dormNameAr,
          name_en: this.dorm.dormNameEn,
          status: this.status,
          address_ar: this.dorm.dormAddress
        }
      }).then(this.$router.push("/dorms/createGroup")).catch(error => {
        this.err = error.response.data.errors;
      });
    },
    UpdateDorm() {
      axios({
        method: "PUT",
        url: store.state.domain + "/dorm/" + this.$route.params.id,
        headers: {
          localization: store.state.localization
        },
        data: {
          name_ar: this.dorm.dormNameAr,
          name_en: this.dorm.dormNameEn,
          status: this.status,
          address_ar: this.dorm.dormAddress
        }
      }).then(res => {
        if (res.status == 200 || res.status == 201) {
            this.$router.push("/dorms")
          }
        }).catch(error => {
          this.err = error.response.data.errors;
      });
    },
  }
};
</script>
<style lang="scss">
form {
  margin: 50px 0;
  .row {
    margin-bottom: 20px;
  }
  label {
    color: #6aa3e3;
  }
  input {
    background: transparent;
    border: 0;
    border-bottom: 1px solid #707070;
    padding: 3px 5px;
  }
}
</style>
