<template>
<div>
  <span v-b-modal.modal-3 v-on:click="isHidden = false" class="add-btn">اضافة مكونات</span>
  <b-modal hide-footer id="modal-3" title="اضافة مكونات" v-if="isHidden == false">
  <div class="add-category">
    <div class="add-category-pop-up">
      <div class="mid-header">
        <h2>اضافه المكونات</h2>
        <b-row>
          <b-col xl="12" md="12">
            <b-form-select
              disabled-field="notEnabled"
              v-model="ingredient.ingredientId"
            >
              <b-form-select-option :value="null" disabled>اختار التصنيف الفرعي</b-form-select-option>
              <b-form-select-option
                v-for="(ingredient, index) in ingredientData"
                :key="index"
                :value="ingredient.id"
              >
                {{ ingredient.name_ar }}
              </b-form-select-option>
            </b-form-select>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="12" xl="12">
            <label>ادخل المكونات باللغه العربيه</label>
            <b-form-input
              type="text"
              v-model="ingredient.ingredientNameAr"
              :state="ingredientArState"
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="12" xl="12">
            <label>ادخل المكونات باللغه الانجليزيه</label>
            <b-form-input
              type="text"
              v-model="ingredient.ingredientNameEn"
              :state="ingredientEnState"
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row v-if="err.length != 0">
        <ul class="form--error">
          <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
          <li
            v-if="
              this.err.name_ar == 'The name ar field is required.'">
            اسم التصنيف باللغة العربية
          </li>
          <li
            v-if="
              this.err.name_en == 'The name en field is required.'">
            اسم التصنيف باللغة الانجليزية
          </li>
            <li
            v-if="
              this.err.subcategory_id == 'The subcategory id field is required.'">
            اختار التصنيف الفرعي
          </li>
          <li
            v-if="
              this.err.name_ar == 'The name ar has already been taken.'">
            التصنيف موجود مسبقا
          </li>
          <li
            v-if="
              this.err.name_en == 'The name en has already been taken.'">
            التصنيف موجود مسبقا
          </li>
        </ul>
      </b-row>
        <div class="save-btn">
          <button type="submit" class="blueBg" @click.prevent="OnSubmit">
            حفظ
          </button>
        </div>
      </div>
    </div>
  </div>
  </b-modal>
  </div>
</template>
<script>
import axios from "axios";
import store from "@/store/index.js";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  props: ["ingredientApi", "addSubCategoryApi", "reloadIngredientsResults"],
  name: "addIngredient",
  data() {
    return {
      err: [],
      ingredientData: [],
      ingredient: {
        ingredientId: null,
        ingredientNameAr: "",
        ingredientNameEn: ""
      },
      isHidden: false
    };
  },
  mounted() {
    axios
      .get(store.state.domain + this.addSubCategoryApi, config)
      .then(res => (this.ingredientData = res.data.data));
  },
  computed: {
    ingredientArState() {
      if (this.ingredient.ingredientNameAr.length == 0) {
        return null;
      } else if (
        !this.ingredient.ingredientNameAr.match(/[\u0600-\u06FF\u0750-\u077F]/)
      ) {
        return false;
      } else if (this.ingredient.ingredientNameAr.length > 0) {
        return true;
      } else {
        return false;
      }
    },
    ingredientEnState() {
      if (this.ingredient.ingredientNameEn.length == 0) {
        return null;
      } else if (!this.ingredient.ingredientNameEn.match(/^[a-zA-Z\s]*$/)) {
        return false;
      } else if (this.ingredient.ingredientNameEn.length > 0) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    OnSubmit: function() {
      axios({
        method: "POST",
        url: store.state.domain + this.ingredientApi,
        config,
        data: {
          subcategory_id: this.ingredient.ingredientId,
          name_ar: this.ingredient.ingredientNameAr,
          name_en: this.ingredient.ingredientNameEn
        }
      })
      .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.isHidden= true,
            this.ingredient.ingredientNameAr = '',
            this.ingredient.ingredientNameEn = '',
            this.reloadIngredientsResults()
          } else{
            this.isHidden= false
          }
        } 
      ).catch(error => {
          this.err = error.response.data.errors;
      });
    },
  }
};
</script>

<style lang="scss">
  .add-btn{
        background-color: #FFF;
    border-radius: 3px;
    padding: 9px 28px;
    color: rgba(0, 0, 0, 0.445);
    font-weight: 600;
    margin: 0 20px;
  }
</style>