<template>
  <section class="RegistrationForm">
    <form>
      <b-container>
        <WelcomeRegisterForm
          v-if="RegisterStep === 1"
          :filesName="this.files"
        />
        <StepOne v-if="RegisterStep === 2" />
        <StepTwo v-if="RegisterStep === 3" />
        <StepFour v-if="RegisterStep === 4" :filesName="this.files" />
        <RegisterSubmit v-if="RegisterStep === 5" />
      </b-container>
    </form>
  </section>
</template>
<script>
import WelcomeRegisterForm from "../components/RegistrationForm/WelcomeRegisterForm";
import StepOne from "../components/RegistrationForm/RegistrationConditions";
import StepTwo from "../components/RegistrationForm/RegistrationStudentInfo";
import StepFour from "../components/RegistrationForm/RegisterFormStepFour";
import RegisterSubmit from "../components/RegistrationForm/RegisterSubmitMsg";
import store from "../store/index.js";
import { mapState } from "vuex";
import axios from "axios";
const config = {
          headers: {
            localization: store.state.localization
        }
};
export default {
  name: "RegisterMainStructure",
  components: {
    WelcomeRegisterForm,
    StepOne,
    StepTwo,
    StepFour,
    RegisterSubmit,
  },
  data() {
    return {
      step: store.state.RegisterStep,
      files: null
    };
  },
  mounted() {
    var $this = this;
    axios.get(store.state.domain + "/applicationDocument", config).then(res => {
      const lengthArray = Object.keys(res.data.data).length;
      $this.files = res.data.data;
      for (let i = 0; i < lengthArray; i++) {
        $this.files[i];
      }
    });
  },
  computed: mapState(["RegisterStep"])
};
</script>

<style lang="scss" scoped>
.RegistrationForm {
  margin-top: 20px;
  .container > div {
    margin: auto;
  }
}
</style>
