<template>
  <div class="main-content-admin">
    <Dormstabs tab="floors" />
    <form>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>اختار المدينة</label>
          <b-form-select
            :class="floorInfo.dorm != null ? 'is-valid' : ''"
            v-model="floorInfo.dorm"
            disabled-field="notEnabled"
            @change="floorInfoBuilding"
          >
            <b-form-select-option :value="null" disabled
              >اختار المدينة</b-form-select-option
            >
            <b-form-select-option
              v-for="dorm in dorms"
              :key="dorm.id"
              :value="dorm.id"
              >{{ dorm.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5" class="data-item">
          <label>اختار المجموعة</label>
          <b-form-select v-model="floorInfo.group" disabled-field="notEnabled" :class="floorInfo.group != null ? 'is-valid' : ''">
            <b-form-select-option :value="null" disabled
              >اختار المجموعة</b-form-select-option
            >
            <b-form-select-option
              v-for="group in groups"
              :key="group.id"
              :value="group.id"
              >{{ group.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="5" class="data-item" offset-lg="1">
          <label>اختار المبنى</label>
          <b-form-select
            :class="floorInfo.building != null ? 'is-valid' : ''"
            v-model="floorInfo.building"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار المبنى</b-form-select-option
            >
            <b-form-select-option
              v-for="building in buildings"
              :key="building.id"
              :value="building.id"
              >{{ building.name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5">
          <label>الحالة</label>
          <b-form-select
            v-model="floorInfo.status"
            :options="options"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار الحالة</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>رقم الدور</label>
          <b-form-input
            type="text"
            v-model="floorInfo.floorNo"
            :state="floorNoState"
            required
          ></b-form-input>
          <b-form-invalid-feedback>
            أدخل رقم الدور
          </b-form-invalid-feedback>
        </b-col>
        <b-col lg="5" class="data-item">
          <label>مشرف الدور</label>
          <b-form-input
            type="text"
            v-model="floorInfo.supervisor"
            :state="supervisorState"
            required
          ></b-form-input>
          <b-form-invalid-feedback>
            أدخل اسم مشرف الدور
          </b-form-invalid-feedback>
        </b-col>
      </b-row>
      <b-row>
        <b-col lg="5" class="data-item" offset-lg="1">
          <label>الكلية</label>
          <b-form-select
            :class="floorInfo.faculty != null ? 'is-valid' : ''"
            v-model="floorInfo.faculty"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >الكلية</b-form-select-option
            >
            <b-form-select-option
              v-for="faculty in faculties"
              :key="faculty.id"
              :value="faculty.id"
              >{{ faculty.faculty_name_ar }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <b-col lg="5">
          <label>الجنسية</label>
          <b-form-select
            :class="floorInfo.nationality != null ? 'is-valid' : ''"
            v-model="floorInfo.nationality"
            :options="nationality"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
          >
            <b-form-select-option :value="null" disabled
              >اختار الجنسية</b-form-select-option
            >
          </b-form-select>
        </b-col>
      </b-row>
      <b-row v-if="err.length != 0">
        <ul class="form--error">
          <p>البيانات غير مكتملة أو غير صحيحة، من فضلك أكمل الآتي:</p>
          <li
            v-if="this.err.building_id == 'The building id field is required.'"
          >
            اختار المبنى
          </li>
          <li v-if="this.err.status == 'The status field is required.'">
            أدخل الحالة
          </li>
          <li
            v-if="
              this.err.floor_number ==
                'The floor number has already been taken.'
            "
          >
            رقم الدور موجود بالفعل
          </li>
        </ul>
      </b-row>
      <div class="save-btn">
        <button
          type="submit"
          class="blueBg"
          @click.prevent="OnSubmit"
          v-if="recordForUpdate == null"
        >
          حفظ
        </button>
        <button
          type="submit"
          class="blueBg"
          @click.prevent="UpdateFloor"
          v-if="recordForUpdate != null"
        >
          تعديل
        </button>
        <button
          type="submit"
          class="blueBg"
          @click.prevent="OnNext"
          v-if="recordForUpdate == null"
        >
          التالى
        </button>
        <span class="redBg" @click="resetData">إلغاء</span>
      </div>
    </form>
    <div class="show-data table-style">
      <table class="grey-style">
        <tr>
          <th>
            <input
              type="checkbox"
              @click="select_all_via_check_box"
              v-model="all_select"
            />
          </th>
          <th>الدور</th>
          <th>الكلية</th>
          <th>الغرف</th>
          <th>الحالة</th>
          <th>المشرف</th>
          <th>تعديل</th>
        </tr>
        <tr v-for="floor in floors" :key="floor.id">
          <td>
            <input type="checkbox" v-model="updateItems" :value="floor.id" />
          </td>
          <td>{{ floor.floor_number }}</td>
          <td>-</td>
          <td>{{ floor.capacity }}</td>
          <td v-if="floor.status == 'Active'" class="greenFont">
            مفعل
          </td>
          <td v-if="floor.status != 'Active'" class="redFont">
            مغلق
          </td>
          <td></td>
          <td>
            <span class="blueBg" @click.prevent="returnFloor(floor.id)"
              >تعديل</span
            >
          </td>
        </tr>
        <tr v-if="emptyData()">
          <td colspan="7"><h4 class="text-center">لا توجد أدوار مسجلة</h4></td>
        </tr>
      </table>

      <div class="tabel-btns-container save-btn" v-if="fullData()">
        <span class="redBg" @click="disableStatus">تعليق</span>
        <span class="greenBg" @click="activeStatus">تفعيل</span>
        <span class="redBg" @click="deleteData">حذف</span>
      </div>
    </div>
  </div>
</template>
<script>
import store from "@/store/index.js";
import axios from "axios";
import Dormstabs from "./DormsTabs";
let config = {
  headers: {
    localization: store.state.localization
  }
};
export default {
  name: "createFloor",
  components: {
    Dormstabs
  },
  data() {
    return {
      err: [],
      recordForUpdate: null,
      all_select: false,
      updateItems: [],
      dorms: "",
      genders: "",
      currentDorm: "",
      groups: "",
      buildings: "",
      floors: [],
      faculties: "",
      floorInfo: {
        dorm: null,
        building: null,
        group: null,
        faculty: null,
        status: null,
        floorNo: "",
        nationality: null,
        supervisor: ""
      },
      nationality: [
        { item: "active", name: "مصري" },
        { item: "off", name: "أجنبي" }
      ],
      options: [
        { item: "active", name: "مفعل" },
        { item: "off", name: "مغلق" }
      ]
    };
  },
  mounted() {
    this.getTableResults();
    axios
      .get(store.state.domain + "/dorm", config)
      .then(res => (this.dorms = res.data.data));
    axios
      .get(store.state.domain + "/buildingGroup", config)
      .then(res => (this.groups = res.data.data));
    axios
      .get(store.state.domain + "/facultiesSelection/listAllFaculties", config)
      .then(res => (this.faculties = res.data.data));
  },
  computed: {
    floorNoState() {
      if (this.floorInfo.floorNo.length == 0) {
        return null;
      } else {
        return true;
      }
    },
    supervisorState() {
      if (this.floorInfo.supervisor.length == 0) {
        return null;
      } else if (
        !this.floorInfo.supervisor.match(/[\u0600-\u06FF\u0750-\u077F]/)
      ) {
        return false;
      } else if (this.floorInfo.supervisor.length > 3) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    getTableResults() {
      axios
        .get(store.state.domain + "/buildingFloor", config)
        .then(res => (this.floors = res.data.data));
    },
    floorInfoBuilding() {
      if (this.floorInfo.dorm == null) {
        return this.floorInfo.building == null;
      } else if (this.floorInfo.dorm > 0) {
        axios
          .get(
            store.state.domain + "/dormBuilding?dorm_id=" + this.floorInfo.dorm, config
          )
          .then(res => (this.buildings = res.data.data)).catch(error => {
          this.err = error.response.data.errors;
        });
      }
    },
    OnSubmit() {
      axios({
        method: "POST",
        url: store.state.domain + "/buildingFloor",
        headers: {
            localization: store.state.localization
        },
        data: {
          building_id: this.floorInfo.building,
          floor_number: this.floorInfo.floorNo,
          status: this.floorInfo.status
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.getTableResults();
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    OnNext() {
      axios({
        method: "POST",
        url: store.state.domain + "/buildingFloor",
        headers: {
          localization: store.state.localization
        },
        data: {
          building_id: this.floorInfo.building,
          floor_number: this.floorInfo.floorNo,
          status: this.floorInfo.status
        }
      })
        .then(res => {
          if (res.status == 200 || res.status == 201) {
            this.getTableResults();
            this.$router.push("/dorms/createPartition");
          }
        })
        .catch(error => {
          this.err = error.response.data.errors;
        });
    },
    resetData() {
      (this.floorInfo.building = null),
        (this.floorInfo.floorNo = ""),
        (this.floorInfo.status = null),
        (this.floorInfo.dorm = null),
        (this.floorInfo.group = null),
        (this.floorInfo.faculty = null),
        (this.floorInfo.nationality = null),
        (this.floorInfo.supervisor = ""),
        (this.recordForUpdate = null);
    },
    deleteData() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios
          .delete(store.state.domain + "/buildingFloor/" + id, { id: id }, config)
          .then(res => {
            if (res.status == 200 || res.status == 201) {
              $this.getTableResults();
            }
          }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    returnFloor(FloorID) {
      this.recordForUpdate = FloorID;
      axios.get(store.state.domain + "/buildingFloor/" + FloorID, config).then(res => {
        (this.floorInfo.dorm = res.data.data.building.dorm),
          (this.floorInfo.group = res.data.data.building.group.id),
          (this.floorInfo.building = res.data.data.building.id),
          (this.floorInfo.status = res.data.data.status),
          (this.floorInfo.floorNo = res.data.data.floor_number),
          (this.floorInfo.faculty = res.data.data.faculties);
      }).catch(error => {
          this.err = error.response.data.errors;
      });
    },
    activeStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/buildingFloor/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "active"
          }
        }).then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    disableStatus() {
      const $this = this;
      this.updateItems.forEach(function(id) {
        axios({
          method: "PUT",
          url: store.state.domain + "/buildingFloor/" + id,
          headers: {
            localization: store.state.localization
          },
          data: {
            status: "off"
          }
        }).then(res => {
          if (res.status == 200 || res.status == 201) {
            $this.getTableResults();
          }
        }).catch(error => {
          this.err = error.response.data.errors;
        });
      });
    },
    UpdateFloor() {
      axios({
        method: "PUT",
        url: store.state.domain + "/buildingFloor/" + this.recordForUpdate,
        headers: {
          localization: store.state.localization
        },
        data: {
          building_id: this.floorInfo.building,
          floor_number: this.floorInfo.floorNo,
          status: this.floorInfo.status
        }
      }).then(res => {
        if (res.status == 200 || res.status == 201) {
          this.getTableResults();
        }
      }).catch(error => {
          this.err = error.response.data.errors;
      });
    },
    select_all_via_check_box() {
      if (this.all_select == false) {
        this.all_select = true;
        this.floors.forEach(floor => {
          this.updateItems.push(floor.id);
        });
      } else {
        this.all_select = false;
        this.updateItems = [];
      }
    },
    emptyData() {
      return this.floors.length < 1;
    },
    fullData() {
      return this.floors.length >= 1;
    }
  }
};
</script>
